import { html, customElement, property, TemplateResult } from 'lit-element';
import { ifDefined } from '../../directives/if-defined';
import { spaceCamel } from '../../utilities/space-camel';
import { VvInputElement } from '../../elements/vv-input-element';
import type { VvInputAutocomplete } from './vv-input';

@customElement('vv-select-fieldset')
export class VvSelectFieldset extends VvInputElement {
    @property()
    label?: string;
    @property()
    labelClass?: string;
    
    @property({type: Boolean})
    nullable: boolean = false;
    @property()
    nullableLabel?: string;

    @property({type: Array})
    options: any[] = [];
    @property()
    optionTextProperty?: string;
    @property()
    optionValueProperty?: string;
    @property()
    optionGroupByProperty?: string;
    @property()
    optionGroupTextProperty?: string;

    @property({type: Boolean})
    multiple?: boolean;
    @property({type: Number})
    size?: number;
    @property()
    name?: string;
    @property()
    class?: string;
    @property()
    selectClass?: string;
    @property()
    fieldsetclass?: string;
    @property({type: Boolean})
    disabled?: boolean;
    @property({type: Number})
    maxlength?: number;
    @property()
    placeholder?: string;
    @property()
    autocomplete?: VvInputAutocomplete;
    @property({type: Boolean})
    required?: boolean;
    @property({type: Boolean})
    modified = false;

    protected setValueFromEventTarget(event: Event) {
        const select = event.target as HTMLSelectElement;
        let value = this.optionValueProperty !== undefined && this.optionValueProperty !== null
            ? select.value
            : this.options[select.selectedIndex];
        if (typeof (value) === "number") {
            value = value.toString();
        }
        if (!value) {
            value = "";
        }
        this.value = value;
        this.modified = true;
    }

    render() {
        var requiredIndicator = this.required ? html`<vv-required-indicator></vv-required-indicator>` : '';
        var showRequiredError = this.required && this.modified && (this.value||'').toString().trim().length == 0
        var inputErrorClasses = showRequiredError ? "vv-input-danger" : "";
        let options = this.getRenderedOptions();

        return html`
        <fieldset class="text-left ${this.fieldsetclass}">
            <label for="${ifDefined(this.name)}" class=${ifDefined(this.labelClass)}>
                ${this.label || spaceCamel(this.name)}
                ${requiredIndicator}
            </label>
            <select
                name=${ifDefined(this.name)}
                id=${ifDefined(this.name)}
                class=${ifDefined(`${this.selectClass} ${inputErrorClasses}`)}
                size=${ifDefined(this.size)}
                autocomplete=${ifDefined(this.autocomplete)}
                placeholder=${ifDefined(this.placeholder)}
                maxlength=${ifDefined(this.maxlength)}
                ?disabled=${this.disabled}
                @change=${this.handleChange}
                value=${ifDefined(this.value)}
                .value=${this.value || ''}
            >${this.nullable ? html`
                <option value="" style="display: none;" hidden disabled 
                    ?selected=${!this.value} .selected=${!this.value}>
                    ${this.nullableLabel}
                </option>
                ` : ""}
            ${options}
            </select>
            ${showRequiredError ? 
                html`<p class="text-sm mb-1 text-red-500">This field is required</p>` 
                : ''}
        </fieldset>
        `;
    }

    groupBy = (items: any[], groupByKey: string) => {
        return items.reduce((groupedItems: Map<any, any>, item: any) => {
            let group = item[groupByKey] || "";
            if (groupedItems.has(group)) {
                groupedItems.get(group).push(item);
            } else {
                groupedItems.set(group, [item]);
            }
            return groupedItems;
        }, new Map<any, any>());
    };

    getRenderedOption = (option: any) => {
        let value = (this.optionValueProperty !== undefined && this.optionValueProperty !== null 
                ? option[this.optionValueProperty] 
                : option);
        if (typeof value === "number") {
            value = value.toString();
        };
        const text = this.optionTextProperty ? option[this.optionTextProperty] : option;
        const selected = this.value !== undefined && this.value !== null && this.value.toString() === value;
        return html`
            <option value=${value || ""} ?selected=${selected} .selected=${selected}>
                ${text}
            </option>
        `;
    };

    getRenderedOptions = () => {
        let options: TemplateResult[] = [];
        if (this.optionGroupByProperty ) {
            const groups = this.groupBy(this.options ?? [], this.optionGroupByProperty);
            const groupKeys = [...groups.keys()].sort();
            for (const groupKey of groupKeys) {
                const group = groups.get(groupKey);
                const groupText = this.optionGroupTextProperty ? group[0][this.optionGroupTextProperty] : groupKey;
                if (groupText) {
                    options.push(html`
                        <optgroup label=${groupText}>
                            ${group.map((option: any) => this.getRenderedOption(option))}
                        </optgroup>
                    `);
                } else {
                    options.push(html`${group.map((option: any) => this.getRenderedOption(option))}`);
                }
            }
        } else if (this.options) {
            options = this.options?.map((option: any) => this.getRenderedOption(option));
        }
        return options;
    };
}