import { html, customElement } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-card-header')
export class VvCardHeader extends VvElement {

    render() {
        return html`
            <h1 class="text-lg font-bold p-2.5 border-b-2 border-gray-200">
                <slot></slot>
            </h1>
        `
    }
}
