import { html, customElement, property } from 'lit-element';
import { VvInputElement } from '../../elements/vv-input-element';
import { spaceCamel } from '../../utilities/space-camel';
import { ifDefined } from '../../directives/if-defined';
import type { VvInputType, VvInputAutocomplete } from './vv-input';

@customElement('vv-input-fieldset')
export class VvInputFieldset extends VvInputElement {

    typesWithCheckedAndAppendedLabel = ['radio', 'checkbox'];
    @property()
    label?: string;
    @property()
    labelClass?: string;
    @property()
    type: VvInputType = 'text';
    @property({ type: Boolean })
    checked?: boolean;
    @property()
    name?: string;
    @property()
    class?: string;
    @property()
    fieldsetclass?: string;
    @property({ type: Boolean })
    disabled?: boolean;
    @property({ type: Number })
    maxlength?: number;
    @property()
    placeholder?: string;
    @property()
    autocomplete?: VvInputAutocomplete;
    @property({ type: Boolean })
    autofocus: boolean = false;
    @property({ type: Boolean })
    required?: boolean;
    @property({ type: Boolean })
    modified = false;
    @property({ type: Boolean })
    showError?: boolean = false;
    @property()
    errorMessage?: string;
    @property()
    errorMessageHiddenType?: `hidden` | `invisible` = `hidden`;
    @property({ type: Boolean })
    showCharacterLimit?: boolean = false;


    protected setValueFromEventTarget(event: Event) {
        const input = event.target as HTMLInputElement;
        this.value = input.value;
        if (this.typesWithCheckedAndAppendedLabel.includes(this.type)) {
            this.checked = input.checked;
        }
        this.modified = true;
    }
    _renderEmptySpace() {
        return this.errorMessageHiddenType === `invisible` ? html`&nbsp;` : ``;
    }

    render() {
        var requiredIndicator = this.required ? html`<vv-required-indicator></vv-required-indicator>` : '';
        var showRequiredError = this.required && this.modified && (this.value || '').trim().length == 0;
        let showErrorMessage = showRequiredError || this.showError;
        let errorMessage = this.errorMessage ? this.errorMessage : (showRequiredError ? `This field is required` : ``);
        let hiddenErrorClass = showErrorMessage ? `` : this.errorMessageHiddenType;
        var inputErrorClasses = showErrorMessage ? "vv-input-danger" : "";
        let showCharacterLimit = this.showCharacterLimit === true && this.maxlength && this.maxlength > 0;
        let currentValueLength = this.value && this.value.length > 0 ? this.value.length : 0;
        return html`
            <fieldset class="text-left ${this.fieldsetclass}">
            ${!this.typesWithCheckedAndAppendedLabel.includes(this.type)
                ? html`<label for="${ifDefined(this.name)}" class=${ifDefined(this.labelClass)}>${this.label || spaceCamel(this.name)} ${requiredIndicator}</label>`
                : html``
            }
            <input
                type=${this.type}
                value=${ifDefined(this.value)}
                .value=${this.value || ''}
                ?checked=${this.checked}
                .checked=${!!this.checked}
                name=${ifDefined(this.name)}
                id=${ifDefined(this.name)}
                class=${ifDefined(`${this.class} ${inputErrorClasses}`)}
                autocomplete=${ifDefined(this.autocomplete)}
                placeholder=${ifDefined(this.placeholder)}
                maxlength=${ifDefined(this.maxlength)}
                ?disabled=${this.disabled}
                ?autofocus=${this.autofocus}
                @input=${this.handleValueChange} 
                @change=${this.handleChange}
            >${this.typesWithCheckedAndAppendedLabel.includes(this.type)
                ? html`<label for="${ifDefined(this.name)}" class=${ifDefined(this.labelClass)}>${this.label || spaceCamel(this.name)} ${requiredIndicator}</label>`
                : html``}
            ${showCharacterLimit
                ? html`<p class="text-sm mb-0">${currentValueLength}/${this.maxlength}</p>`
                : html``}
            <p class="text-sm mb-1 text-red-500 -mt-1 ${hiddenErrorClass}">${errorMessage}${this._renderEmptySpace()}</p>
            </fieldset>`
    }
}