import { html, customElement, css, unsafeCSS, property } from 'lit-element';
import TomSelect from 'tom-select';
import { append, hash_key, isKeyDown, preventDefault } from '../../utilities/vv-combo.utils';
import * as constants from 'tom-select/src/constants';
import { iterate } from '@orchidjs/sifter/lib/utils';
import { TomCreateCallback, TomOption, TomTemplateNames } from 'tom-select/dist/types/types/core';
import TomSelectStyles from '../../../content/styles/tom-select.css';
import { VvElement } from '../../elements/vv-element';
import { VvButton } from '../controls/vv-button';
import { removeClasses, setAttr } from 'tom-select/src/vanilla';
import { highlight, removeHighlight } from 'tom-select/src/contrib/highlight';

@customElement('vv-combo')
export class VvCombo extends VvElement {
    static styles = [
        VvElement.baseStyles,
        css`${unsafeCSS(TomSelectStyles)}`,
        css`
        .no-results{
            text-align: center;
        }
        .ts-dropdown .spinner{
            display: flex;
            justify-content: center;
            width: auto;
        }
        .ts-dropdown {
            overflow-y: hidden;
            max-height: 20rem;
        }
        .ts-dropdown .option.create{
            color: var(--vv-black);
        }
        
        .has-items .ts-control > input {
            margin: 0px !important;
        }
        .ts-dropdown .option:not(.selected) vv-icon-add, 
        .ts-dropdown .option:not(.selected) vv-icon-check,
        .ts-dropdown .option i.vv-icon
        {
            display: none;
        }
        .ts-dropdown .option.selected i.vv-icon,
        .ts-dropdown .option.create i.vv-icon {
          display:inline;
        }
        .ts-dropdown .option-inner{
            display: inline-block;
            vertical-align: top;
            max-width: 88%;
        }
        `,
    ]

    tomSelect?: TomSelect;
    @property()
    private searchValue: string = '';
    @property()
    placeholder?: string;
    @property()
    noResultsText?: string;
    @property({ type: Array })
    options: any = [];
    @property()
    optionTextProperty?: string;
    @property()
    optionValueProperty?: string;
    @property()
    optionGroupByProperty?: string;
    @property()
    optionGroupTextProperty?: string;
    @property({ type: Object })
    load?: Function;
    @property({ type: Object })
    addclick?: Function;
    @property({ type: Object })
    createFilter?: Function;
    @property()
    addButtonText?: string;
    @property({ type: Boolean })
    allowCreate?: boolean = false;
    @property()
    createdItemGroupValue?: string;
    @property({ type: Number })
    minLength?: number = 0;
    @property({ type: Number })
    maxLength?: number = 1000;
    @property()
    maxDropdownWidth?: number;

    private iconClass: string = 'z-10 absolute right-2 top-1/2 transform -translate-y-1/2 text-vv-grey-dark';

    _renderIcon() {
        return this.searchValue && this.searchValue.length > 0 ? this._renderClose() : this._renderSearch();
    }

    _renderClose() {
        return html`
        <button class="${this.iconClass}" title="Clear Search" aria-label="Clear Search" @click=${() => this.clearSearchClicked()}>
            <vv-icon-close></vv-icon-close>
        </button>
        `
    }

    clearSearchClicked() {
        this.clearEverything();
        let searchInput: any = this.shadowRoot!.querySelector(`#vv-combo-${this.id}-ts-control`);
        searchInput.focus();
    }

    _renderSearch() {
        return html`
        <span class="${this.iconClass}" aria-hidden="true">
            <vv-icon-search></vv-icon-search>
        </span>`;
    }

    protected firstUpdated(): void {
        let element = this.shadowRoot!.querySelector(`#vv-combo-${this.id}`);
        let vvCombo = this;
        let addContainerSelectorId: string = `vv-combo-${vvCombo.id}-vv-add-container`;
        let addSearchedTopicsButtonId: string = `add-searched-topics-button`;
        if (element && vvCombo) {
            this.tomSelect = new TomSelect(<any>element, {
                placeholder: vvCombo.placeholder,
                valueField: vvCombo.optionValueProperty,
                labelField: vvCombo.optionTextProperty,
                searchField: [],
                optgroupField: vvCombo.optionGroupByProperty,
                persist: true,
                create: vvCombo.allowCreate,
                closeAfterSelect: false,
                hideSelected: false,
                loadThrottle: 300,
                onBlur: function () {
                    vvCombo.clearEverything();
                },
                shouldLoad: function (query) {
                    vvCombo.searchValue = query;
                    let isValid: boolean = vvCombo.isInputValid(vvCombo.searchValue);
                    if (!isValid) return false;
                    return true;
                },
                createFilter: function (input) {
                    if (vvCombo.createFilter) {
                        let isValid: boolean = vvCombo.createFilter(input);
                        if (!isValid) return false;
                    }
                    else {
                        let isValid: boolean = vvCombo.isInputValid(input);
                        if (!isValid) return false;
                    }

                    let self: any = this;
                    input = input.toLowerCase();

                    for (var obj in self.options) {
                        if (self.options[obj][vvCombo.optionTextProperty || `label`].toLowerCase() == input)
                            return false;
                    }
                    return true;
                },
                load: function (query, callback) {
                    vvCombo.load!(query, function (options: any, optGroups: any) {
                        vvCombo.clearItemsAndOptions();
                        callback(options, optGroups);
                    }, vvCombo);
                },
                onLoad: function () {
                    let self: any = this;
                    let existingContainerElement = self.dropdown.querySelector(`#${addContainerSelectorId}`);
                    if (vvCombo.comboBoxHasSearchResultOptions(self) && !existingContainerElement)
                        vvCombo.createAddButton(self, addContainerSelectorId, vvCombo, addSearchedTopicsButtonId);
                },
                onChange() {
                    let self: any = this;
                    let existingContainerElement = self.dropdown.querySelector(`#${addContainerSelectorId}`);
                    if (existingContainerElement) {
                        let vvButton: VvButton = existingContainerElement.querySelector(`#${addSearchedTopicsButtonId}`);
                        vvButton.disabled = self.items.length > 0 ? false : true;
                    }
                    else
                        vvCombo.createAddButton(self, addContainerSelectorId, vvCombo, addSearchedTopicsButtonId);
                },
                onDropdownClose: function () {
                    let self: any = this;
                    let addContainerElement = self.dropdown.querySelector(`#${addContainerSelectorId}`);
                    addContainerElement?.remove();
                },
                render: {
                    no_results: function (_data, escape) {
                        return `<div class="no-results text-sm text-vv-grey-dark">${escape(vvCombo.noResultsText || 'No Results Found')}</div>`;
                    },
                    option: function (data, escape) {
                        return `<div title="${escape(data[vvCombo.optionTextProperty || `label`])}"><span class="option-inner vv-ellipsis">${escape(data[vvCombo.optionTextProperty || `label`])}</span><span class="float-right"><i class="vv-icon black-check"></i></span></div>`;
                    },
                    option_create: function (data, escape) {
                        return `<div  title="${escape(data.input)}" class="option create"><span class="option-inner vv-ellipsis">${escape(data.input)}</span><span class="float-right text-vv-grey-dark"><i class="vv-icon grey-add"></i></span></div>`;
                    },
                    item: function () {
                        return '<span></span>';
                    },
                    dropdown: function () {
                        return `<div></div>`;
                    },
                    optgroup_header: function (data, escape) {
                        return `<div class="bg-vv-grey-light text-vv-grey-dark uni-neue-700 py-1.5 px-2">${escape(data[vvCombo.optionGroupTextProperty || `label`])}</div>`;
                    }
                }
            });

            this.tomSelect!.hook('instead', 'refreshOptions', (triggerDropdown: boolean = true) => {
                var i, j, k, n, optgroup, optgroups, html: DocumentFragment, has_create_option, active_value, active_group;
                var create;
                const groups: { [key: string]: DocumentFragment } = {};

                const groups_order: string[] = [];
                var self: any = this.tomSelect!;
                var query = self.inputValue();
                var results = self.search(query);
                var active_option = null; //self.activeOption;
                var show_dropdown = self.settings.shouldOpen || false;
                var dropdown_content = self.dropdown_content;

                if (self.activeOption) {
                    active_value = self.activeOption.dataset.value;
                    active_group = self.activeOption.closest('[data-group]') as HTMLElement;
                }

                // build markup
                n = results.items.length;
                if (typeof self.settings.maxOptions === 'number') {
                    n = Math.min(n, self.settings.maxOptions);
                }

                if (n > 0) {
                    show_dropdown = true;
                }

                // render and group available options individually
                for (i = 0; i < n; i++) {

                    // get option dom element
                    let opt_value = results.items[i].id;
                    let option = self.options[opt_value];
                    let option_el = self.getOption(opt_value, true) as HTMLElement;


                    // toggle 'selected' class
                    if (!self.settings.hideSelected) {
                        option_el.classList.toggle('selected', self.items.includes(opt_value));
                    }

                    optgroup = option[self.settings.optgroupField] || '';
                    optgroups = Array.isArray(optgroup) ? optgroup : [optgroup];

                    for (j = 0, k = optgroups && optgroups.length; j < k; j++) {
                        optgroup = optgroups[j];
                        if (!self.optgroups.hasOwnProperty(optgroup)) {
                            optgroup = '';
                        }
                        if (!groups.hasOwnProperty(optgroup)) {
                            groups[optgroup] = document.createDocumentFragment();
                            groups_order.push(optgroup);
                        }

                        // nodes can only have one parent, so if the option is in mutple groups, we need a clone
                        if (j > 0) {
                            option_el = option_el.cloneNode(true) as HTMLElement;
                            setAttr(option_el, { id: option.$id + '-clone-' + j, 'aria-selected': null });
                            option_el.classList.add('ts-cloned');
                            removeClasses(option_el, 'active');
                        }

                        // make sure we keep the activeOption in the same group
                        if (!active_option && active_value == opt_value) {
                            if (active_group) {
                                if (active_group.dataset.group === optgroup) {
                                    active_option = option_el;
                                }
                            } else {
                                active_option = option_el;
                            }
                        }

                        groups[optgroup].appendChild(option_el);
                    }
                }

                // sort optgroups
                if (self.settings.lockOptgroupOrder) {
                    groups_order.sort((a, b) => {
                        var a_order = self.optgroups[a] && self.optgroups[a].$order || 0;
                        var b_order = self.optgroups[b] && self.optgroups[b].$order || 0;
                        return a_order - b_order;
                    });
                }

                // render optgroup headers & join groups
                html = document.createDocumentFragment();
                iterate(groups_order, (optgroup) => {
                    if (self.optgroups.hasOwnProperty(optgroup) && groups[optgroup].children.length) {

                        let group_options = document.createDocumentFragment();
                        let header = self.render('optgroup_header', self.optgroups[optgroup]);
                        append(group_options, header);
                        append(group_options, groups[optgroup]);

                        let group_html = self.render('optgroup', { group: self.optgroups[optgroup], options: group_options });

                        append(html, group_html);

                    } else {
                        append(html, groups[optgroup]);
                    }
                });

                dropdown_content.innerHTML = '';
                append(dropdown_content, html);

                // highlight matching terms inline
                if (self.settings.highlight) {
                    removeHighlight(dropdown_content);
                    if (results.query.length && results.tokens.length) {
                        iterate(results.tokens, (tok) => {
                            highlight(dropdown_content, tok.regex);
                        });
                    }
                }

                // helper method for adding templates to dropdown
                var add_template = (template: TomTemplateNames) => {
                    let content = self.render(template, { input: query });
                    if (content) {
                        show_dropdown = true;
                        dropdown_content.insertBefore(content, dropdown_content.firstChild);
                    }
                    return content;
                };

                // add loading message
                if (self.loading) {
                    add_template('loading');
                } else if (!self.settings.shouldLoad.call(self, query)) {
                    add_template('not_loading');
                }

                // add create option
                has_create_option = self.canCreate(query);
                if (has_create_option) {
                    create = add_template('option_create');
                }

                // activate
                self.hasOptions = results.items.length > 0 || has_create_option;
                if (show_dropdown) {

                    if (results.items.length > 0) {

                        if (!active_option && self.settings.mode === 'single' && self.items.length) {
                            active_option = self.getOption(self.items[0]);
                        }

                    } else if (create) {
                        active_option = create;
                    }

                    if (triggerDropdown && !self.isOpen) {
                        self.open();
                        self.scrollToOption(active_option, 'auto');
                    }
                    self.setActiveOption(active_option);

                } else {
                    self.clearActiveOption();
                    if (triggerDropdown && self.isOpen) {
                        self.close(false); // if create_option=null, we want the dropdown to close but not reset the textbox value
                    }
                }

                //important empty if's here. for the way the no_results renders.
                if (self.loading) {
                } else if (!self.settings.shouldLoad.call(self, query)) {
                } else if (results.items.length === 0)
                    add_template('no_results');
            });

            this.tomSelect!.hook('instead', 'onKeyDown', (e: KeyboardEvent): void => {
                var self: any = this.tomSelect!;

                if (self.isLocked) {
                    if (e.keyCode !== constants.KEY_TAB) {
                        preventDefault(e);
                    }
                    return;
                }

                switch (e.keyCode) {

                    // ctrl+A: select all
                    case constants.KEY_A:
                        if (isKeyDown(constants.KEY_SHORTCUT, e)) {
                            if (self.control_input.value == '') {
                                preventDefault(e);
                                self.selectAll();
                                return;
                            }
                        }
                        break;

                    // esc: close dropdown
                    case constants.KEY_ESC:
                        if (self.isOpen) {
                            preventDefault(e, true);
                            self.close();
                        }
                        self.clearActiveItems();
                        return;

                    // down: open dropdown or move selection down
                    case constants.KEY_DOWN:
                        if (!self.isOpen && self.hasOptions) {
                            self.open();
                        } else if (self.activeOption) {
                            let next = self.getAdjacent(self.activeOption, 1);
                            if (next) self.setActiveOption(next);
                        } else if (!self.activeOption) {
                            let next = self.selectable()[0] as HTMLElement;
                            if (next) self.setActiveOption(next);
                        }
                        preventDefault(e);
                        return;

                    // up: move selection up
                    case constants.KEY_UP:
                        if (self.activeOption) {
                            let prev = self.getAdjacent(self.activeOption, -1);
                            if (prev) self.setActiveOption(prev);
                        }
                        preventDefault(e);
                        return;

                    // return: select active option
                    case constants.KEY_RETURN:
                        if (self.canSelect(self.activeOption)) {
                            self.onOptionSelect(e, self.activeOption!);
                            preventDefault(e);

                            // if the option_create=null, the dropdown might be closed
                        } else if (self.settings.create && self.createItem()) {
                            preventDefault(e);
                        }
                        return;

                    // left: modifiy item selection to the left
                    case constants.KEY_LEFT:
                        self.advanceSelection(-1, e);
                        return;

                    // right: modifiy item selection to the right
                    case constants.KEY_RIGHT:
                        self.advanceSelection(1, e);
                        return;

                    // tab: select active option and/or create item
                    case constants.KEY_TAB:

                        if (self.settings.selectOnTab) {
                            if (self.canSelect(self.activeOption)) {
                                self.onOptionSelect(e, self.activeOption!);

                                // prevent default [tab] behaviour of jump to the next field
                                // if select isFull, then the dropdown won't be open and [tab] will work normally
                                preventDefault(e);
                            }
                            if (self.settings.create && self.createItem()) {
                                preventDefault(e);
                            }
                        }
                        return;

                    // delete|backspace: delete items
                    case constants.KEY_BACKSPACE:
                    case constants.KEY_DELETE:
                        self.deleteSelection(e);
                        return;
                }

                // don't enter text in the control_input when active items are selected
                if (self.isInputHidden && !isKeyDown(constants.KEY_SHORTCUT, e)) {
                    preventDefault(e);
                }
            });

            this.tomSelect!.hook('instead', 'createItem', (input: null | string = null, _triggerDropdown: boolean = true, callback: TomCreateCallback = () => { }): boolean => {
                var self: any = this.tomSelect!;
                var caret = self.caretPos;
                var output;
                input = input || self.inputValue();

                if (!self.canCreate(input)) {
                    callback();
                    return false;
                }

                self.lock();

                var created = false;
                var create = (data?: boolean | TomOption) => {
                    self.unlock();

                    if (!data || typeof data !== 'object') return callback();
                    var value = hash_key(data[self.settings.valueField]);
                    if (typeof value !== 'string') {
                        return callback();
                    }
                    if (this.optionGroupByProperty && this.createdItemGroupValue)
                        data[this.optionGroupByProperty] = this.createdItemGroupValue;

                    self.setTextboxValue();
                    self.addOption(data, true);
                    self.setCaret(caret);
                    self.addItem(value);
                    callback(data);
                    created = true;
                };

                if (typeof self.settings.create === 'function') {
                    output = self.settings.create.call(this, input, create);
                } else {
                    output = {
                        [self.settings.labelField]: input,
                        [self.settings.valueField]: input,
                    };
                }

                if (!created) {
                    create(output);
                }

                return true;
            });

            //hooked into the built in onOptionSelect function to add code for deselecting.
            this.tomSelect!.hook('instead', 'onOptionSelect', (evt: KeyboardEvent, option: HTMLElement) => {
                let self: any = this.tomSelect!;
                if (option.classList.contains('selected')) {
                    self.removeItem(option.dataset.value);
                    self.refreshOptions();
                } else {
                    var value;

                    // should not be possible to trigger a option under a disabled optgroup
                    if (option.parentElement && option.parentElement.matches('[data-disabled]')) {
                        return;
                    }

                    if (option.classList.contains('create')) {
                        self.createItem(null, true, () => {
                            if (self.settings.closeAfterSelect) {
                                self.close();
                            }
                        });
                        self.refreshItems();
                    } else {
                        value = option.dataset.value;
                        if (typeof value !== 'undefined') {
                            self.lastQuery = null;
                            self.addItem(value);
                            if (self.settings.closeAfterSelect) {
                                self.close();
                            }

                            if (!self.settings.hideSelected && evt.type && /click/.test(evt.type)) {
                                self.setActiveOption(option);
                            }
                        }
                    }
                }
            });
        }
    }
    isInputValid(input: string): boolean {
        if (!input || input.length < this.minLength! || input.length > this.maxLength!) return false;

        return true;
    }

    private createAddButton(self: any, addContainerSelectorId: string, vvCombo: this, addSearchedTopicsButtonId: string) {
        let containerElement = document.createElement('div');
        containerElement.id = addContainerSelectorId;
        [`flex`, `py-2`, `justify-center`, `border-t`, `border-solid`, `border-vv-grey-mid`].forEach(x => containerElement.classList.add(x));
        let buttonElement = document.createElement('button');
        buttonElement.id = addSearchedTopicsButtonId;
        buttonElement.type = `button`;
        buttonElement.classList.add(`vv-btn-primary`);
        buttonElement.disabled = true;
        buttonElement.innerHTML = `${vvCombo.addButtonText}`;
        buttonElement.addEventListener('click', function (e) {
            vvCombo.addclick!(self.items, function () {
                vvCombo.clearEverything();
                self.close();
            });
            return e;
        });
        containerElement.appendChild(buttonElement);
        self.dropdown.appendChild(containerElement);
    }

    comboBoxHasSearchResultOptions(self: any) {
        return Object.keys(self.options).length > 0;
    }
    clearEverything() {
        this.searchValue = '';
        this.tomSelect?.setTextboxValue('');
        this.clearItemsAndOptions();
    }

    clearItemsAndOptions() {
        this.tomSelect!.clear();
        this.tomSelect!.clearOptions();
        this.tomSelect!.clearOptionGroups();
    }


    render() {
        return html`
            <style>
                .ts-dropdown.multi {
                    ${this.maxDropdownWidth ? `width: ${this.maxDropdownWidth};` : ``}
                }                
            </style>
            <div id="${this.id}-search-container" class="vv-search align-center justify-items-center relative w-full" role="search">
                <select multiple id='vv-combo-${this.id}' >
                <option></option>
                </select>
                ${this._renderIcon()}
            </div>
        `
    }
}