import { html, customElement } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-accordion')
export class VvAccordion extends VvElement {

    private _handleSlotChange() {
        const accordionSections = this.querySelectorAll('vv-accordion-section');

        if (accordionSections.length > 1) {
            accordionSections.forEach((section) => {
                section.classList.add('mb-4');
            });
        }

        accordionSections.forEach((section) => {
            section.addEventListener('click', () => {
                this._handleClick(accordionSections, section);
            });
        });
    }

    private _handleClick(accordionSections: NodeListOf<Element>, clickedSection: Element) {
        const isOpen = this._sectionIsOpen(clickedSection);

        accordionSections.forEach((section) => {
            if (section !== clickedSection) {
                if (isOpen && this._sectionIsOpen(section)) {
                    section.removeAttribute('open');
                }
            }
        });
    }

    private _sectionIsOpen(section: Element) {
        return section.getAttribute('open') !== null &&  section.getAttribute('open') !== undefined;
    }

    render() {
        return html`
            <div role="group" tabindex="-1">
                <slot @slotchange=${this._handleSlotChange}></slot>
            </div>
        `
    }
}
