import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-accordion-section')
export class VvAccordionSection extends VvElement {

    @property()
    header = '';

    @property({ type: Boolean, reflect: true })
    open?= false;

    @property()
    headerButtonStyle = '';

    toggle() {
        const eventName = !this.open ? 'accordion-expand' : 'accordion-collapse';
        const event = new CustomEvent(eventName, { bubbles: true, composed: true });
        this.dispatchEvent(event);

        this.open = !this.open;
    }

    connectedCallback() {
        super.connectedCallback();
        this.classList.add('flex');
        this.classList.add('flex-col');
    }

    renderButton() {
        return html`
            <vv-button type="tertiary" class="text-left justify-between w-full overflow-hidden" @click=${() => this.toggle()}
                buttonStyle=${this.headerButtonStyle}>
                <span class="text-lg font-bold line-clamp-1">
                    ${this.header || html`<slot name="header"></slot>`}
                </span>
                <vv-selector-button ?open="${this.open}" rotate="right"></vv-selector-button>
            </vv-button>
        `;
    }

    renderContent() {
        const contentOpenClass = this.open ? 'scale-y-100' : 'scale-y-0 h-0';

        return html`
        <div class="border-t-2 border-gray-200 transform ease-in-out origin-top duration-300 ${contentOpenClass}">
            <slot></slot>
        </div>`
    }

    render() {
        return html`
            <article class="flex flex-col flex-1 bg-white shadow-md rounded">
                ${this.renderButton()}
                ${this.renderContent()}
            </article>
        `
    }
}
