import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-skeleton-loader')
export class VvSkeletonLoader extends VvElement {

    @property()
    class?: string;

    @property({ type : Number })
    width? : Number = undefined;

    @property({ type : Number })
    height? : Number = undefined;

    @property()
    type: 'text' | 'circle'  = 'text';

    @property()
    animation: 'pulse' | 'none' = 'pulse';

    _getClasses() {
        const classes: string[] = ['block', 'bg-gray-200', 'h-full'];

        if (this.animation === 'pulse') classes.push('animate-pulse');
        if (this.type === 'circle') classes.push('rounded-full');

        return `${classes.join(' ')} ${this.class !== undefined ? this.class : ''}`;
    }

    _getStyle() {
        const styles: string[] = [];

        if (this.width) styles.push(`width: ${this.width}px`);
        if (this.height) styles.push(`height: ${this.height}px`);
        
        return styles.join(';');
    }

    render() {
        const classes = this._getClasses();
        const styles = this._getStyle();
        
        return html`
            <span class="${classes}" style="${styles}"></span>
        `;
    }
}
