import { html, customElement, state } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

// For TailwindCSS Purge -- JIT Mode doesn't support whitelisting yet.
// grid-cols-1 grid-cols-2 grid-cols-3 grid-cols-4 grid-cols-5 grid-cols-6 grid-cols-7 grid-cols-8
// grid-cols-9 grid-cols-10 grid-cols-11 grid-cols-12
@customElement('vv-kpi-block-group')
export class VvKpiBlockGroup extends VvElement {

    @state()
    private _gridColsClass : string = '';

    @state()
    private _gridGapClass : string = '';

    handleSlotChange() {
        this._gridGapClass = this.childElementCount < 5 ? ' gap-4' : '';
        this._gridColsClass = `grid-cols-${this.childElementCount}`;
    }

    render() {
        const containerClass = `pl-0 pr-0 grid ${this._gridColsClass}${this._gridGapClass}`;

        return html`
            <div class="${containerClass}" role="list">
                <slot @slotchange=${this.handleSlotChange}></slot>
            </div>
        `
    }
}
