import { html, customElement } from "lit-element";
import { BaseElement } from "./BaseElement";

@customElement('vv-required-indicator')
export class RequiredIndicator extends BaseElement {
  
  render() {
    return html`
      <span class="w-1 h-1 m-1 bg-red-500 rounded-2xl inline-block">
      </span>
      <vv-tooltip>  
        <div class="px-4 py-2">This field is required.</div>
      </vv-tooltip>
    `;
  }
}