import { html, customElement, property} from 'lit-element';
import { VvElement } from '../../elements/vv-element';
import { ifDefined } from '../../directives/if-defined';

@customElement('vv-collapse')
export class VvCollapse extends VvElement {

    @property()
    class = '';

    @property()
    header = '';

    @property()
    closingHeader = '';

    @property()
    headerClass = '';

    @property({type: Boolean})
    open? = false;

    @property({type: Boolean})
    top? = false;

    toggle() {
        this.open = !this.open;
    }

    renderButton() {
        const header = this.open && this.closingHeader ? this.closingHeader : this.header;
        
        return html`
        <vv-button type="tertiary" class="inline-flex" @click=${() => this.toggle()}>
            <vv-selector-button ?open="${this.open}">
                <span class="${this.headerClass}">${header}</span>
            </vv-selector-button>
        </vv-button>
        `
    }

    renderContent() {
        const contentOpenClass = this.open ? 'scale-y-100' : 'scale-y-0 h-0';

        return html`
        <div class="transform ease-in-out origin-top duration-200 ${contentOpenClass}">
            <slot></slot>
        </div>`
    }

    render() {
        return this.top 
        ? html`<div class="${this.class}" aria-expanded="${ifDefined(this.open)}">${this.renderContent()}${this.renderButton()}</div>`
        : html`<div class="${this.class}" aria-expanded="${ifDefined(this.open)}">${this.renderButton()}${this.renderContent()}</div>`;
    }
}