export function spaceCamel(camelCasedString?: string) {
    if (camelCasedString === undefined || camelCasedString === null) return;
    let result = camelCasedString.charAt(0).toUpperCase() + camelCasedString.slice(1)
    result = result.replace(/([a-z])([A-Z])/g, '$1 $2')
    .replace(/([A-Z])([a-z])/g, ' $1$2')
    .replace(/\ +/g, ' ').trim();
    return (result.toLowerCase().endsWith(' id')) 
            ? result.slice(0, result.length - 3) 
            : result;
}