import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-flyout')
export class VvFlyout extends VvElement {

    @property()
    class = '';

    @property()
    title = '';

    @property({type: Boolean})
    visible?: boolean;

    @property()
    size: 'sm' | 'lg' = 'sm';

    _onClose() {
        this.visible = false;
        const event = new CustomEvent('flyout-closed', {bubbles: true, composed: true});
        this.dispatchEvent(event);
    }

    render() {
        const sizeClasses = this.size === 'sm' ? 'w-11/12 md:w-1/2 lg:w-1/3' : 'w-11/12 lg:w-1/2';
        return html`
        <aside class="fixed top-0 right-0 flex flex-col ${sizeClasses} h-screen shadow-md rounded bg-vv-white transform transition-all ease-in-out duration-300 ${this.visible ? 'visible -translate-x-0' : 'invisible translate-x-full'} ${this.class}">
            <div class="flex justify-between items-center m-4 pb-2 border-b-2 border-gray-200">
                <slot name="title">
                    <span class="text-xl uni-neue-700">${this.title}</span>
                </slot>
                <vv-button type="icon" @click=${this._onClose}>
                    <span class="sr-only">Close Flyout</span>
                    <vv-icon-close size="24" aria-hidden="true"></vv-icon-close>
                </vv-button>
            </div>
            
            <div class="px-4 pb-5 overflow-auto uni-neue">
                <slot></slot>
            </div>
        </aside>`
    }
}
