import { html, customElement, property, PropertyValues} from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-modal')
export class VvModal extends VvElement {

    @property()
    class = '';

    @property()
    modalBodyClass = '';

    @property()
    modalBackgroundClass = '';

    /** Message to display as the header title of the modal.
     * 
     * NOTE: Unfortunately the title attribute causes the entire modal 
     * to show the message as a tooltip when the mouse is over it.
     * Use the 'header' attribute instead if you only want to display in the header.
     */
    @property()
    title = '';

    /** This property is the same as 'title', but does not show in a tooltip 
     * when hovering over the modal. 
     */
    @property()
    header = '';

    @property()
    confirmType : 'confirm' | 'danger' = 'confirm';

    @property()
    confirmText = '';

    @property()
    cancelText = 'Cancel';
    
    @property({type: Boolean})
    visible?: boolean;    

    @property()
    closeOnConfirm : string = 'true';

    @property({type: Boolean})
    disabled?: boolean;

    static openModals = new Set<VvModal>();
    updated(changedProperties: PropertyValues) {
        if (changedProperties.has("visible")) {
            if (this.visible) {
                VvModal.openModals.add(this);                
            } else {
                VvModal.openModals.delete(this);
            }

            if (VvModal.openModals.size > 0) {
                document.body.classList.add('overflow-hidden');
            } else {                
                document.body.classList.remove('overflow-hidden');
            }
        }
        super.updated(changedProperties);
    }

    _onClose() {
        this.visible = false;
        const event = new CustomEvent('modal-closed', {bubbles: true, composed: true});
        this.dispatchEvent(event);
    }

    _onConfirm() {
        if (this.closeOnConfirm === 'true') this.visible = false;
        const event = new CustomEvent('modal-confirmed', {bubbles: true, composed: true});
        this.dispatchEvent(event);
    }

    _renderConfirmButton() {
        return this.confirmText ? html`<vv-button type="${this.confirmType === 'confirm' ? 'primary' : 'danger'}" @click=${this._onConfirm} ?disabled=${this.disabled}>${this.confirmText}</vv-button>` : html``;
    }

    render() {
        return html`
        <div class="fixed top-0 left-0 w-full h-full overflow-auto bg-black bg-opacity-30 z-10 ${this.modalBackgroundClass} ${this.visible ? 'visible' : 'invisible'}" tabindex="-1" role="dialog">
            <div class="absolute top-10 left-0 right-0 mx-auto shadow-md rounded bg-vv-white min-w-min transition-opacity duration-300 ${this.visible ? 'opacity-100' : 'opacity-0'} ${this.class}" role="document">
                <div class="flex justify-between items-center m-4 pb-2 border-b-2 border-gray-200">
                    <span class="text-xl uni-neue-700">${this.header || this.title}</span>
                    <vv-button type="icon" @click=${this._onClose}>
                        <span class="sr-only">Close Flyout</span>
                        <vv-icon-close size="24" aria-hidden="true"></vv-icon-close>
                    </vv-button>
                </div>
                
                <div class="px-4 max-h-screen-3/4 overflow-auto uni-neue ${this.modalBodyClass}">
                    <slot></slot>
                </div>

                <div class="flex justify-end gap-2 p-4 items-center">
                    <div class="flex-1">
                        <slot name="button-line-message"></slot>
                    </div>
                    ${this.cancelText ? html`
                    <vv-button type="secondary" @click=${this._onClose}>${this.cancelText}</vv-button>
                    `: ''}
                    ${this._renderConfirmButton()}
                </div>
            </div>
        </div>`
    }    
}
