import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement("vv-icon-facebook")
export class VvIconFacebook extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
      :host {
        width: auto;
        height: fit-content;
      }
    `,
  ];
  
  @property({type: Boolean})
  disabled = false;
  
  @property({ type: Number })
  size = 18;

  render() {
    const fillColor = this.disabled ?  '#939598' : '#333333';
    return html`
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="${this.size}"
        height="${this.size}"
        viewBox="0 0 18 18"
        fill="none"
      >
        <path
          d="M11.7944 5.68145H10.5067C10.3136 5.68145 9.8629 5.98405 9.8629 6.32527V7.6129H11.7944V9.54436H9.8629V14.0511H7.93145V9.54436H6V7.6129H7.93145V6.32527C7.93145 4.90243 9.19333 3.75 10.5454 3.75H11.7944V5.68145V5.68145V5.68145V5.68145Z"
          fill="${fillColor}"
        />
      </svg>
    `;
  }
}
