import { html, customElement } from 'lit-element';
import { VvDateRangeElement } from './vv-daterange-element';
import { ifDefined } from '../../directives/if-defined';

@customElement('vv-daterange-simple')
export class VvDateRangeSimple extends VvDateRangeElement {

    connectedCallback() {
        super.connectedCallback();

        const classes = this.class?.split(/[ ]+/) || [];
        classes.forEach(($class) => {
            this.classList.add($class)
        });
        this.classList.add('items-center');

        if (this.startDate) {
            this.endMin = this.startDate;
        }

        if (this.endDate) {
            this.startMax = this.endDate;
        }
    }

    _handleStartChanged(e : CustomEvent) {
        this.endMin = e.detail.value;
        const event = new CustomEvent('start-changed', {bubbles: true, composed: true, detail: e.detail});
        this.dispatchEvent(event);
    }

    _handleEndChanged(e : CustomEvent) {
        this.startMax = e.detail.value;
        const event = new CustomEvent('end-changed', {bubbles: true, composed: true, detail: e.detail});
        this.dispatchEvent(event);
    }

    render() {
        return html`
            <vv-datepicker
                @duetChange="${this._handleStartChanged}"
                value="${ifDefined(this.startDate)}" 
                label="${ifDefined(this.startLabel)}"
                placeholder="${ifDefined(this.startPlaceholder)}"
                min="${ifDefined(this.startMin)}"
                max="${ifDefined(this.startMax)}"
                error="${ifDefined(this.startDateError)}"
                ?disabled=${this.disabled}
                ?required=${this.required}
            ></vv-datepicker>
            <span class="pl-4 pr-4">to</span>
            <vv-datepicker
                @duetChange="${this._handleEndChanged}"
                value="${ifDefined(this.endDate)}" 
                label="${ifDefined(this.endLabel)}"
                placeholder="${ifDefined(this.endPlaceholder)}"
                min="${ifDefined(this.endMin)}"
                max="${ifDefined(this.endMax)}"
                error="${ifDefined(this.endDateError)}"
                ?disabled=${this.disabled}
                ?required=${this.required}
            ></vv-datepicker>
        `
    }
}
