import { css, LitElement, unsafeCSS } from "lit-element";
import { ReactiveController, ReactiveControllerHost } from "./Controllers/ReactiveController";
import styles from '../../content/styles/vv-tailwind.css';

export class BaseElement extends LitElement implements ReactiveControllerHost {  
  static styles = css`${unsafeCSS(styles)}`;
  
  controllers = new Set<ReactiveController>();
  addController(controller: ReactiveController): void {
    this.controllers.add(controller);
  }
  removeController(controller: ReactiveController): void {
    this.controllers.delete(controller);
  }

  connectedCallback() {
    super.connectedCallback();
    this.controllers.forEach(controller => 
      controller.hostConnected?.());
  }
  disconnectedCallback() {
    super.disconnectedCallback();
    this.controllers.forEach(controller =>
      controller.hostDisconnected?.());
  } 
  update(changedProperties: Map<string | number | symbol, unknown>) {
    this.controllers.forEach(controller => {
      controller.hostUpdate?.();
    });
    super.update(changedProperties);
  }
  updated() {
    this.controllers.forEach(controller => {
      controller.hostUpdated?.();
    });
  }
}
