import {AttributePart, directive, Part} from 'lit-html';

const previousValues = new WeakMap<Part, unknown>();

export const ifDefined = directive((value: unknown) => (part: Part) => {
  const previousValue = previousValues.get(part);

  if ((value === undefined || value === null) && part instanceof AttributePart) {
    if (previousValue !== undefined || previousValue !== null || !previousValues.has(part)) {
      const name = part.committer.name;
      part.committer.element.removeAttribute(name);
    }
  } else if (value !== previousValue) {
    part.setValue(value);
  }

  previousValues.set(part, value);
});