import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-mobile')
export class VvIconMobile extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ]

  @property({type: Number})
  size = 24;

  render() {
    return html`
        <svg width=${this.size} height=${this.size} viewBox="0 0 14 22" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
            <path d="M11 0H3C1.34 0 0 1.34 0 3V19C0 20.66 1.34 22 3 22H11C12.66 22 14 20.66 14 19V3C14 1.34 12.66 0 11 0ZM9 20H5V19H9V20ZM12.25 17H1.75V3H12.25V17Z" fill="currentColor"/>
        </svg>
    `;
  }
}
