import { Context, EvaluationContract, USA2PBrand } from "../contracts";
import { MessageController } from "./MessageController";
import { QueryController } from "./QueryController";
import { ReactiveControllerHost } from "./ReactiveController";

export class QueryUSA2PBrandController {
  constructor(
    private host: ReactiveControllerHost,
    private messageController: MessageController,
    private context: () => Context,
  ) {
    this.usA2PBrands = new QueryController(this.host, this.messageController, this.context, this.getAll);
    this.usA2PBrand = new QueryController(this.host, this.messageController, this.context, this.get);
  }

  public readonly usA2PBrands: QueryController<USA2PBrand[]>;
  public readonly usA2PBrand: QueryController<USA2PBrand>;

  reload = async (isProcessing?: boolean) => {
    await this.usA2PBrands.reload(isProcessing);
    await this.usA2PBrand.reload(isProcessing);
  }

  startNew = () => {
    this.messageController.clear();
    this.usA2PBrand.data = {
      customerProfileSid: this.context().customerProfileSid,
      companyType: this.context().isStandardCustomerProfile ? "" : "starter",
    };
    this.usA2PBrand.serverHasNewData = false;
    this.usA2PBrand.isDirty = true;
    this.context().trustProductSid = "";
    this.context().campaignUseCaseSid = "";
    this.host.requestUpdate();
  }

  select = async (sid: string) => {
    this.messageController.clear();
    this.usA2PBrand.data = undefined;
    this.usA2PBrand.serverHasNewData = false;
    this.usA2PBrand.isDirty = false;
    this.context().trustProductSid = sid;
    this.context().campaignUseCaseSid = "";
    await this.reload(true);
  }

  private getAll = async () => {
    // if (!this.context().customerProfileSid) {
    //   this.usA2PBrand.data = undefined;
    //   return [];
    // }
    const url = `/USA2PBrands?associationId=${this.context().associationId}&customerProfileSid=${this.context().customerProfileSid}`;
    const result = await this.usA2PBrands.getJson<USA2PBrand[]>(url);
    if (result && !this.usA2PBrand.isDirty) {
      const sid = this.context().trustProductSid;
      const usA2PBrand = this.usA2PBrand.data;
      const noneToLoad = !sid && !usA2PBrand;
      const hasAtLeastOne = (noneToLoad && result?.length); // Pick the first one.
      // The CustomerProfile doesn't seem to exist anymore.
      const doesNotExistAnymore = (usA2PBrand?.sid && !result?.some(x => x.sid === usA2PBrand.sid));
      const firstSid = result?.[0]?.sid ?? "";
      if ((hasAtLeastOne || doesNotExistAnymore) && sid !== firstSid) {
        await this.select(firstSid);
      } else if (noneToLoad) {
        this.startNew();
      }
    }
    return result;
  }

  private get = async () => {
    if (!this.context().trustProductSid) {
      return this.usA2PBrand.data;
    }
    const url = `/USA2PBrands/${this.context().trustProductSid}?associationId=${this.context().associationId}`;
    const brand = await this.usA2PBrand.getJson<USA2PBrand>(url);
    this.context().brandRegistrationSid = brand.brandRegistrationSid;
    this.context().isNonProfit = brand.companyType === "non-profit";
    this.context().isBrandRegistered = brand.status === "twilio-approved";
    return brand;
  }
  
  update = async () => {
    if (!this.usA2PBrand.data) {
      this.messageController.clearAndSet("US A2P Brands not loaded yet.");
      return;
    }
    const url = `/USA2PBrands/${this.usA2PBrand.data.sid ?? '_'}?associationId=${this.context().associationId}`;
    const results = await this.usA2PBrand.postJson<EvaluationContract>(url, this.usA2PBrand.data);
    this.usA2PBrand.isDirty = false;
    this.messageController.clearAndSet("Saved!", "main", 5000);
    if (results.status === 'noncompliant') {
      this.messageController.addValidationErrors(results);
    }
    this.context().trustProductSid = results.trust_product_sid;
    await this.reload(true);
  }

  submit = async (isMockBrand: boolean) => {
    if (!this.usA2PBrand.data) {
      this.messageController.clearAndSet("US A2P Brand not loaded yet.");
      return;
    }
    const url = `/USA2PBrands/${this.usA2PBrand.data.sid}/submit?associationId=${this.context().associationId}`;
    const results = await this.usA2PBrand.postJson<EvaluationContract>(url, { isMockBrand: isMockBrand });
    this.usA2PBrand.isDirty = false;
    this.messageController.clearAndSet("Submitted!", "main", 5000);
    if (results.status === 'noncompliant') {
      this.messageController.addValidationErrors(results);
    }
    await this.reload(true);
  }

  delete = async () => {
    if (!this.usA2PBrand.data) {
      this.messageController.clearAndSet("US A2P Brand not loaded yet.");
      return;
    }
    const url = `/USA2PBrands/${this.usA2PBrand.data.sid}?associationId=${this.context().associationId}`;
    const success = await this.usA2PBrand.deleteJson<boolean>(url, {});
    if (!success) {
      this.messageController.clearAndSet("Unable to delete US A2P Brand.");
    } else {
      this.messageController.clearAndSet("Deleted!", "main", 5000);
      this.usA2PBrand.serverHasNewData = false;
      this.usA2PBrand.isDirty = false;
    }
    await this.select("");
  }
}