import { css, html, customElement } from 'lit-element';
import { VvElement } from '../../elements/vv-element';
import { VvSegmentedControlButton } from './vv-segmented-control-button';

@customElement('vv-segmented-control-group')
export class VvSegmentedControlGroup extends VvElement {

    static vvSegmentedControlGroupStyles = css`
        ::slotted(vv-segmented-control-button:first-of-type) {
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
        }
        ::slotted(vv-segmented-control-button:last-of-type) {
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
        }
        ::slotted(vv-segmented-control-button:not(:last-of-type)) {
            border-top-right-radius: 0px;
            border-bottom-right-radius: 0px;
        }
    `
    static styles = [VvElement.baseStyles, VvSegmentedControlGroup.vvSegmentedControlGroupStyles];

    connectedCallback() {
        super.connectedCallback();

        const buttons = this.querySelectorAll('vv-segmented-control-button');
        buttons.forEach((button) => {
            button.addEventListener('click', () => {
                return this._handleClick(buttons, button as VvSegmentedControlButton);
            });
        });
    }

    private _handleClick(buttons: NodeListOf<Element>, button: VvSegmentedControlButton) {
        if (button.active || button.disabled) return;
        
        buttons.forEach((button) => {
            button.removeAttribute('active');
        });

        button.setAttribute('active', '');
    }

    render() {
        return html`
            <div class="inline-flex" role="group" tabindex="-1">
                <slot></slot>
            </div>
        `
    }
}
