import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-subtract-circle')
export class VvIconSubtractCircle extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ]

  @property({type: Number})
  size = 27;

  // #6A6A6A
  
  render() {
    return html`
    <svg width="${this.size}" height="${this.size}" viewBox="0 0 27 27" fill="currentcolor" xmlns="http://www.w3.org/2000/svg">
      <circle cx="13.5" cy="13.5" r="13.5" />
      <rect x="6" y="12" width="15" height="2.5" rx="1" fill="none" />
    </svg>
    `;
  }
}