import { html, customElement, property } from 'lit-element';
import { ifDefined } from '../../directives/if-defined';
import { spaceCamel } from '../../utilities/space-camel';
import { VvInputElement } from '../../elements/vv-input-element';

@customElement('vv-textarea-fieldset')
export class VvTextAreaFieldset extends VvInputElement {

    @property()
    label?: string;
    @property()
    name?: string;
    @property()
    class?: string;
    @property()
    fieldsetclass?: string;
    @property({type: Number})
    rows?: number;
    @property({type: Number})
    cols?: number;
    @property({type: Boolean})
    disabled?: boolean;
    @property({type: Number})
    maxlength?: number;
    @property()
    placeholder?: string;
    @property()
    autocomplete?: string;
    @property()
    wrap?: "soft" | "hard";

    render() {
        return html`
        <fieldset class=${ifDefined(this.fieldsetclass)}>
            <label for="${ifDefined(this.name)}">${this.label || spaceCamel(this.name)}</label>
            <textarea
                name=${ifDefined(this.name)}
                id=${ifDefined(this.name)}
                class=${ifDefined(this.class)}
                rows=${ifDefined(this.rows)}
                cols=${ifDefined(this.cols)}
                wrap=${ifDefined(this.wrap)}
                placeholder=${ifDefined(this.placeholder)}
                maxlength=${ifDefined(this.maxlength)}
                ?disabled=${this.disabled}
                @input=${this.handleValueChange} 
                @change=${this.handleChange}            
            >${this.value}</textarea></fieldset>
        `
    }
}