import { html, customElement, property } from 'lit-element';
import dayjs from 'dayjs';
import localizedFormat from 'dayjs/plugin/localizedFormat';
dayjs.extend(localizedFormat)

import { VvElement } from '../../elements/vv-element';

@customElement('vv-report-status')
export class VvReportStatus extends VvElement {

    @property({type: Boolean})
    isActive = false;

    @property()
    activeText = 'Active'

    @property()
    inactiveText = 'Inactive'

    @property()
    startDate = '';

    @property()
    endDate = '';

    renderActiveIcon() {
        if (this.isActive) return html`<vv-icon-active class="inline-block pr-1"></vv-icon-active> <span>${this.activeText}</span>`;
        return html`<vv-icon-inactive class="inline-block pr-1"></vv-icon-inactive> <span class="text-vv-grey-power">${this.inactiveText}</span>`;
    }

    renderDates() {
        const startDate = dayjs(this.startDate);
        const endDate = dayjs(this.endDate);
        
        if (startDate.isValid() && !endDate.isValid()) return startDate.format('LL');
        if (!startDate.isValid() && endDate.isValid()) return endDate.format('LL');
        if (startDate.isValid && endDate.isValid()) return `${startDate.format('LL')} - ${endDate.format('LL')}`;
        return '';
    }

    render() {
        return html`
            <div class="flex justify-end items-center text-sm uni-neue-700">${this.renderActiveIcon()}</div>
            <div class="text-right text-sm">${this.renderDates()}</div>
        `
    }
}
