import { customElement, html, property } from "lit-element";
import { BaseElement } from "./BaseElement";
import { Context } from "./contracts";
import { TwilioCampaignUseCasesDataController } from "./Controllers/TwilioCampaignUseCasesDataController";
import { RadioEnum } from "./enums";

@customElement("twilio-campaign-use-cases")
export class TwilioCampaignUseCases extends BaseElement {
  @property({type: Object})
  context: Context = {} as Context;
  @property()
  campaignInReviewMessage = "";

  private controller = new TwilioCampaignUseCasesDataController(this, () => this.context);
  private useCaseOptions = () => this.context.isStandardCustomerProfile
    ? this.whittledUseCases() : [ [ "STARTER", "Starter campaign for customers with low traffic volumes.", "" ] ] as RadioEnum;
  private standardUseCases = [
    // Added the new "LOW_VOLUME" use case, since Twilio is asking for this instead of the deprecated "Starter" registrations.
    [ "LOW_VOLUME", "Low Volume", "If you plan to send less than 6000 messages per day." ],
    [ "MIXED", "Standard", "A campaign that covers multiple use cases. Choose this option if no other options make sense for your use case." ],
    [ "HIGHER_EDUCATION", "Higher Education", "Message campaigns from colleges, universities, and other education institutions." ],
  ] as RadioEnum;
  private whittledUseCases = () => this.context.isNonProfit ? [
      // Updated some wording on these use cases to make it more clear to the user which one to choose.
      [ "POLITICAL", "Political / 501(c)(4), 501(c)(5), 501(c)(6), or 527", "Part of an organized effort to influence decision making of a specific group." ],
      [ "CHARITY", "Charity / 501(c)(3) Nonprofit", "Communications from a registered Charity / 501(c)(3) Nonprofit aimed at providing help and raising money for those in need. Limited to 501(c)(3) nonprofits." ],
      ...this.standardUseCases,
    ] as RadioEnum
  : this.standardUseCases;

  hostUpdated() {
  }

  render() {
    const data = this.controller.campaignUseCase;
    const textarea = this.controller.campaignUseCaseControls.textarea;
    const checkbox = this.controller.campaignUseCaseControls.checkbox;
    const radio = this.controller.campaignUseCaseControls.radio;
    const disabled = !!(data?.status && data?.status !== "draft") || this.controller?.isProcessing;

    return html`
    <div class="container mx-auto pb-8">
      <hr class="my-4" />
      <div class="col-span-2">
        This information is for carriers so they understand the kinds of messages you'll be sending.
      </div>
      <hr class="my-4" />
      ${(data?.status === "IN_PROGRESS" && this.campaignInReviewMessage) ? html`
        <vv-alert type="warning" class="block mb-4" .showClose=${false}>
        ${this.campaignInReviewMessage}
        <a href="https://support.twilio.com/hc/en-us/articles/11587910480155-A2P-10DLC-Campaign-Vetting-Changes-January-2023">Read more</a>
        </vv-alert>
      ` : ''}

      <div class="grid md:grid-cols-2 gap-4">
        <div class="grid md:grid-cols-2 gap-4">
          ${radio("Campaign Use Case", "usecase", { errorType: "", required: true, 
            disabled: disabled || data?.usecase === "STARTER", 
            options: this.useCaseOptions() })} 
          <div>
            ${textarea("Description", "description", {errorType: "", required: true, disabled })}
            ${checkbox("Messages will include embedded links.", "hasEmbeddedLinks", {errorType: "", disabled, class: "mt-3" })}
            ${checkbox("Messages will include phone numbers.", "hasEmbeddedPhoneNumbers", {errorType: "", disabled, class: "mt-3" })} 
          </div>
          ${textarea("Message Sample #1", "messageSample1", {errorType: "", required: true, disabled })}
          ${textarea("Message Sample #2", "messageSample2", {errorType: "", required: true, disabled })} 
          ${data?.mock
              ? checkbox('Is "mock" campaign', "mock", {disabled: true, errorType: ''}) : ''}
        </div>
        <div>
          <vv-table
            .rowClasses=${(i: number) => {
              const sid = this.context.campaignUseCaseSid;
              const hoverClasses = (!sid || (this.controller.campaignUseCases?.length ?? 0 > 1)) ? "cursor-pointer hover:bg-gray-100" : "";
              const selectedRowClasses = sid === this.controller.campaignUseCases?.[i]?.sid ? "bg-gray-100" : "";
              return `${hoverClasses} ${selectedRowClasses}`;
            }}
            .rowClick=${async (i: number) => {
              if (!this.context.campaignUseCaseSid || ((this.controller.campaignUseCases?.length ?? 0) > 1)) {
                const sid = this.controller.campaignUseCases?.[i]?.sid ?? "";
                await this.controller.selectCampaignUseCase(sid);
              }
            }}
            .cellClasses=${(col: number, row: number) => 
              (col === 0 && this.controller?.campaignUseCases?.[row].status === "draft") 
              ? "bg-yellow-100" : ""
            }
            .head=${[
              'Status',
              'Use Case',
              'Created',
              'Updated',
            ]}
            .body=${this.controller?.campaignUseCases?.map(x =>[
              x.status ?? "",
              x.usecase ?? "",
              x.dateCreated ? new Date(x.dateCreated).toLocaleString() : '',
              x.dateUpdated ? new Date(x.dateUpdated).toLocaleString() : '',
            ]) ?? []}>
          </vv-table>
          ${this.context.isSuperAdmin ? html`
          <div class="mt-4 float-right flex flex-wrap gap-4">
            ${!this.controller.campaignUseCase?.sid ? html`
              <button class="btn-default" 
                ?disabled=${this.controller.isProcessing}
                @click=${this.createNew}>
                Create New Campaign Use Case
              </button>
            ` : ''}
            ${this.controller.campaignUseCase?.sid ? html`
              <button class="vv-btn-danger"
                ?disabled=${this.controller.isProcessing}
                @click=${this.delete}>
                Delete
              </button>
            ` : ''}
          </div>
          ` : ''}
        </div>
      </div>

      <hr class="my-4" />
      <div class="mt-4 flex flex-wrap gap-4">
        ${(!data?.sid || data?.status === "draft") && this.context.isBrandRegistered ? html`
          <button class="btn-primary" @click=${this.submit}
            ?disabled=${this.missingFields()}>
            Register
          </button>
        ` : !this.context.isBrandRegistered ? html`
          Brand must be approved before campaign use case can be registered.
        ` : html`
          Thanks for submitting everything!
        `}
        ${this.controller?.messages.messageComponent()}
      </div>
    </div>
    `;
  }

  private submit = async () => {
    if (confirm("Are you sure that you want to register this campaign?\n(You will not be able to easily change it after submission)")) {
      await this.controller?.submitCampaignUseCase();
    }
  }

  private createNew = async () => {
    if (confirm("Are you sure you want to create a new campaign use case?")) {
      this.controller.startNewCampaignUseCase();
    }
  }

  private delete = async () => {
    if (confirm("Are you sure that you want to delete this campaign use case?")) {
      await this.controller?.deleteCampaignUseCase();
    }
  }

  private missingFields() {
    const data = this.controller?.campaignUseCase;
    return !data?.usecase || !data?.description 
      || !data?.messageSample1 || !data?.messageSample2;
  }
}