import { css, customElement, html, property } from "lit-element";
import { ifDefined } from "../../directives/if-defined";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-check')
export class VvIconCheck extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ]
  @property({type: String})
  class?: String;

  @property({type: Number})
  size = 24;

  render() {
    return html`
    <svg 
    class=${ifDefined(this.class)}
    width=${this.size} height=${this.size} viewBox="0 0 24 24" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
      <path d="M8.79508 15.8749L4.62508 11.7049L3.20508 13.1149L8.79508 18.7049L20.7951 6.70492L19.3851 5.29492L8.79508 15.8749Z" fill="currentColor"/>
    </svg>
    `;
  }
}