import { ReactiveController, ReactiveControllerHost } from "./ReactiveController";
import { MessageController } from "./MessageController";
import { ControlsController } from "./ControlsController";
import { QueryUSA2PBrandController } from "./QueryUSA2PBrandController";
import { Context, USA2PBrand } from "../contracts";

export class TwilioUSA2PBrandsDataController implements ReactiveController {
  constructor(
    private host: ReactiveControllerHost,
    private context: () => Context,
  ){
    host.addController(this);
  }

  private readonly _messageController: MessageController = new MessageController(this.host);
  private readonly _a2pBrands = new QueryUSA2PBrandController(this.host, this._messageController, this.context);
  private readonly _a2pBrandsControls: ControlsController<USA2PBrand> = new ControlsController(this._messageController, this._a2pBrands.usA2PBrand);

  hostUpdated() {
  }

  get messages() { 
    return this._messageController;
  }  
  get usA2PBrands() {
    return this._a2pBrands.usA2PBrands.data;
  }
  get usA2PBrand() {
    return this._a2pBrands.usA2PBrand.data;
  }
  get usA2PBrandControls() {
    return this._a2pBrandsControls;
  }

  get isProcessing() {
    return this._a2pBrands.usA2PBrands.isProcessing
      || this._a2pBrands.usA2PBrand.isProcessing;
  }
  get isDirty() {
    return this._a2pBrands.usA2PBrands.isDirty
      || this._a2pBrands.usA2PBrand.isDirty;
  }
  get serverHasNewData() {
    return this._a2pBrands.usA2PBrands.serverHasNewData
      || this._a2pBrands.usA2PBrand.serverHasNewData;
  }

  updateBrand = async () => {
    return await this._a2pBrands.update();
  }
  submitBrand = async (isMockBrand: boolean) => {
    return await this._a2pBrands.submit(isMockBrand);
  }
  deleteBrand = async () => {
    return await this._a2pBrands.delete();
  }

  clearAndReload = async () => {
    this._a2pBrands.usA2PBrands.serverHasNewData = false;
    this._a2pBrands.usA2PBrands.isDirty = false;
    await this._a2pBrands.reload(true);
  }

  startNew = () => {
    this._a2pBrands.startNew();
  }

  select = async (sid: string) => {
    await this._a2pBrands.select(sid);
  }
}