import { customElement, property } from 'lit-element';
import { html } from 'lit-html';
import { VvElement } from '../../elements/vv-element';

@customElement("vv-selector-button")
export class VvSelectorButton extends VvElement {
  
  @property({attribute: false})
  onClick?: (e: MouseEvent) => {};

  @property({type: Boolean})
  open? = false;

  @property()
  caret?: 'right' | 'left' = 'right';

  @property()
  rotate?: 'right' | 'left' = 'left';

  render() {
    const rotateClass = this.rotate === 'left' ? 'rotate-180' : '-rotate-180';
    return html`
    <div @click=${this.onClick} class="cursor-pointer flex ${this.caret === 'right' ? 'flex-row' : 'flex-row-reverse'} items-baseline gap-1">
      <slot></slot>
      <vv-icon-dropdown-caret class="transform transition duration-200 ${this.open ? rotateClass : 'rotate-0'} inline-block text-vv-black"></vv-icon-dropdown-caret>
    </div>
    `;
  }
}
