import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../core/elements/vv-element';

@customElement('vv-politician-name')
export class VvPoliticianName extends VvElement {
    
    @property()
    title = '';

    @property()
    firstname = '';

    @property()
    nickname = '';

    @property()
    lastname = '';

    @property()
    suffix = '';

    @property()
    party = '';

    @property()
    country? : 'US' | 'CA' = 'US';

    @property()
    state = '';

    @property()
    legtype = '';

    @property()
    chamber = '';

    @property()
    district = '';

    isUsPolitician() {
        // Assume that null/empty country is US
        return !this.country || (this.country && this.country.toLowerCase() === 'us');
    }

    isUsSenator() {
        return this.isUsPolitician() && this.legtype && this.legtype.toLowerCase() === 'u' && this.chamber && this.chamber.toLowerCase() === 's';
    }

    getPoliticianName() {
        const firstName = this.nickname ? this.nickname : this.firstname;
        const suffix = this.suffix ? ` ${this.suffix}` : '';
        return `${this.title} ${firstName} ${this.lastname}${suffix}`;
    }

    getJurisdiction() {
        const arr = [this.party, this.state];

        // Only have districts if not US Senator
        if (!this.isUsSenator()) {
            arr.push(this.district)
        }

        const jurisdiction = arr.filter((a) => a).join('-');
        return jurisdiction.length > 0 ? html`<span class="pl-1 ${this.getPartyColorClass()}">(${jurisdiction})</span>` : '';
    }

    getPartyColorClass() {
        if (this.isUsPolitician()) {
            const party = this.party ? this.party.toLowerCase() : '';
            switch (party) {
                case 'd':
                    return 'text-vv-democrat-party';
                case 'r':
                    return 'text-vv-republican-party';
                case 'i':
                    return 'text-vv-independent-party';
                case 'g':
                    return 'text-vv-green-party';
                case 'l':
                    return 'text-vv-libertarian-party';
                default:
                    return 'text-vv-other-party';
            }
        }
        return '';
    }

    render() {
        return html`
            <span>${this.getPoliticianName()}</span>${this.getJurisdiction()}
        `
    }
}
