import { customElement, html, property, TemplateResult } from "lit-element";
import { BaseElement } from "./BaseElement";

@customElement('vv-table')
export class VvTable extends BaseElement {
  @property({type: Array})
  head: (TemplateResult|string)[] = [];

  @property({type: Array})
  body: (TemplateResult|string)[][] = [];

  @property({type: Object})
  rowClasses?: (row: number) => string;

  @property({type: Object})
  rowClick?: (row: number) => void;

  @property({type: Object})
  cellClasses?: (col: number, row: number) => string;

  @property({type: Object})
  cellClick?: (col: number, row: number) => void;

  private rowClickHandler = (row: number) => () => {
    this.rowClick?.(row);
  }
  private cellClickHandler = (col: number, row: number) => () => {
    this.cellClick?.(col, row);
  }

  render() {
    return html`
    <div class="shadow border-b border-gray-200 sm:rounded-lg overflow-x-auto overflow-y-hidden">
      <table class="w-full divide-y divide-gray-200">
        <thead class="bg-gray-100">
          ${this.head.map(x => html`
          <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
            ${x}
          </th>
          `)}
        </thead>
        <tbody class="overflow-y-auto">
          ${this.body.map((x, row) => html`
          <tr class=${this.rowClasses?.(row) ?? ''}
            @click=${this.rowClickHandler(row)}>
            ${x.map((x, col) => html`
            <td class="px-6 py-4 whitespace-nowrap ${this.cellClasses?.(col, row)}"
              @click=${this.cellClickHandler(col, row)}>
              ${x}
            </td>
            `)}
          </tr>
          `)}
        </tbody>
      </table>
    </div>
    `;
  }
}