import { CampaignUseCase, Context } from "../contracts";
import { MessageController } from "./MessageController";
import { QueryController } from "./QueryController";
import { ReactiveControllerHost } from "./ReactiveController";

export class QueryCampaignUseCaseController {
  constructor(
    private host: ReactiveControllerHost,
    private messageController: MessageController,
    private context: () => Context,
  ) {
    this.campaignUseCasesQuery = new QueryController(this.host, this.messageController, this.context, this.getCampaignUseCases);  
    this.campaignUseCaseQuery = new QueryController(this.host, this.messageController, this.context, this.getCampaignUseCase);
  }

  public readonly campaignUseCasesQuery: QueryController<CampaignUseCase[]>;
  public readonly campaignUseCaseQuery: QueryController<CampaignUseCase>;

  reload = async (isProcessing?: boolean) => {
    await this.campaignUseCasesQuery.reload(isProcessing);
    await this.campaignUseCaseQuery.reload(isProcessing);
  }

  startNewCampaignUseCase = () => {
    this.messageController.clear();
    this.campaignUseCaseQuery.data = {
      usecase: this.context().isStandardCustomerProfile ? "LOW_VOLUME" : "STARTER",
      description: "Our organization sends text messages to our advocates to inform them of key advocacy issues and ask them to take action on campaigns to contact their legislators.",
      messageSample1: "Thanks for signing up to receive text alerts from the [organization display name]",
      messageSample2: "Click the link below to tell Congress to pass HR 5\nhttps://twilio.com/",
      hasEmbeddedLinks: true,
      hasEmbeddedPhoneNumbers: false,
    } as CampaignUseCase;
    this.campaignUseCaseQuery.serverHasNewData = false;
    this.campaignUseCaseQuery.isDirty = true;
    this.context().campaignUseCaseSid = "";
  }

  selectCampaignUseCase = async (sid: string) => {
    this.messageController.clear();
    this.campaignUseCaseQuery.data = undefined;
    this.campaignUseCaseQuery.serverHasNewData = false;
    this.campaignUseCaseQuery.isDirty = false;
    this.context().campaignUseCaseSid = sid;
    await this.reload(true);
  }

  getCampaignUseCases = async () => {
    const url = `/CampaignUseCases/?associationId=${this.context().associationId}&customerProfileSid=${this.context().customerProfileSid}&trustProductSid=${this.context().trustProductSid}`;
    const response = await this.campaignUseCasesQuery.getJson<CampaignUseCase[]>(url);
    if (response && !this.campaignUseCaseQuery.isDirty) {
      const campaignUseCaseSid = this.context().campaignUseCaseSid;
      const campaignUseCase = this.campaignUseCaseQuery.data;
      const nothingToLoad = !campaignUseCaseSid && !campaignUseCase;
      const hasAtLeastOne = (nothingToLoad && response?.length);
      const doesNotExistAnymore = (campaignUseCase?.sid && !response?.some(x => x.sid === campaignUseCase.sid));
      const firstSid = response[0]?.sid ?? "";
      if ((hasAtLeastOne || doesNotExistAnymore) && campaignUseCaseSid !== firstSid) {
        await this.selectCampaignUseCase(firstSid);
      } else if (nothingToLoad) {
        this.startNewCampaignUseCase();
      }
    }
    return response;
  }

  getCampaignUseCase = async () => {
    if (!this.context().campaignUseCaseSid) {
      return this.campaignUseCaseQuery.data;
    }
    const response = await this.campaignUseCaseQuery.getJson<CampaignUseCase>(`/CampaignUseCases/${this.context().campaignUseCaseSid}/?associationId=${this.context().associationId}&customerProfileSid=${this.context().customerProfileSid}`);
    return response;
  }

  submit = async () => {
    if (!this.campaignUseCaseQuery.data) {
      this.messageController.clearAndSet("Campaign Use Case not loaded yet.", "main");
      return;
    }
    // Ensure that Brand SID is set.
    if (!this.context().trustProductSid) {
      this.messageController.clearAndSet("Please select a Brand on the brand page to register this campaign against.");
      return;
    }
    this.campaignUseCaseQuery.data.trustProductSid = this.context().trustProductSid;
    this.campaignUseCaseQuery.data.brandRegistrationSid = this.context().brandRegistrationSid;
    const url = `/CampaignUseCases/${this.campaignUseCaseQuery.data.sid ?? '_'}?associationId=${this.context().associationId}&customerProfileSid=${this.context().customerProfileSid}`;
    const results = await this.campaignUseCaseQuery.postJson<CampaignUseCase>(url, this.campaignUseCaseQuery.data);
    this.campaignUseCaseQuery.isDirty = false;
    this.messageController.clearAndSet("Submitted!", "main", 5000);
    this.context().campaignUseCaseSid = results.sid ?? "";
    await this.reload(true);
  }

  delete = async () => {
    if (!this.campaignUseCaseQuery.data) {
      this.messageController.clearAndSet("Campaign Use Case not loaded yet.", "main");
      return;
    }
    const success = await this.campaignUseCaseQuery.deleteJson<boolean>(`/CampaignUseCases/${this.campaignUseCaseQuery.data.sid}?associationId=${this.context().associationId}&customerProfileSid=${this.context().customerProfileSid}`, {});
    if (!success) {
      this.messageController.clearAndSet("Unable to delete Campaign Use Case.");
    } else {
      this.messageController.clearAndSet("Deleted!", "main", 5000);
      this.campaignUseCaseQuery.serverHasNewData = false;
      this.campaignUseCaseQuery.isDirty = false;
    }    
    try {
      this.campaignUseCaseQuery.isProcessing = true;
      await new Promise(r => setTimeout(r, 1000));
      await this.selectCampaignUseCase("");
    } finally {
      this.campaignUseCaseQuery.isProcessing = false;
    }
  }
}