import { html, customElement } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-accordion-section-body')
export class VvAccordionSectionBody extends VvElement {

    connectedCallback() {
        super.connectedCallback();
        this.classList.add('flex');
        this.classList.add('flex-1');
        this.classList.add('p-2.5');
        this.classList.add('overflow-y-auto');
    }

    render() {
        return html`
            <slot></slot>
        `
    }
}
