import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-three-dots')
export class VvIconThreeDots extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ];

  @property({type: Number})
  size = 24;

  render() {
    return html`
    <svg width=${this.size} height=${this.size} viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
      <circle cx="12" cy="4.5" r="1.5" fill="#6A6A6A"/>
      <circle cx="12" cy="11.731" r="1.5" fill="#6A6A6A"/>
      <circle cx="12" cy="18.9619" r="1.5" fill="#6A6A6A"/>
    </svg>`;
  }
}