import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-desktop')
export class VvIconDesktop extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ]

  @property({type: Number})
  size = 24;

  render() {
    return html`
        <svg width=${this.size} height=${this.size} viewBox="0 0 22 20" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
            <path d="M20 0H2C0.9 0 0 0.9 0 2V14C0 15.1 0.9 16 2 16H9L7 19V20H15V19L13 16H20C21.1 16 22 15.1 22 14V2C22 0.9 21.1 0 20 0ZM20 12H2V2H20V12Z" fill="currentColor"/>
        </svg>
    `;
  }
}
