import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-clone')
export class VvIconClone extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ]

  @property({type: Number})
  size = 24;

  render() {
    return html`
    <svg width="${this.size}" height="${this.size}" viewBox="0 0 24 24" fill="none" stroke="currentcolor" xmlns="http://www.w3.org/2000/svg">
      <path d="M 7 3 C 6.446 3 6 3.446 6 4 L 6 9 L 17 9 C 17.554 9 18 9.446 18 10 L 18 14 L 20 14 C 20.554 14 21 13.554 21 13 L 21 4 C 21 3.446 20.554 3 20 3 L 7 3 z" stroke-width="2" />
      <rect x="3" y="9" width="15" height="11" rx="1" stroke-width="2" />
    </svg>
    `;
  }
}

