import { css, property } from "lit-element";
import { VvElement } from "./vv-element";

export class VvInputElement extends VvElement {
    static styles = [
        VvElement.baseStyles,
        css`select { background-color: #ffffff; }`,
      ]

    @property()
    value?: string;

    protected setValueFromEventTarget(event: Event) {
        const input = event.target as HTMLInputElement;
        this.value = input.value;
    }

    protected handleChange(event: Event) {
        this.setValueFromEventTarget(event);
        let customEvent = new CustomEvent(event.type, { bubbles: true, composed: true, detail: event });
        this.dispatchEvent(customEvent);        
    }

    protected handleValueChange(event: Event) {
        this.setValueFromEventTarget(event);
    }
}