import { css, html, customElement } from 'lit-element';
import { VvInput } from './vv-input';
import { ifDefined } from '../../directives/if-defined';

@customElement('vv-search')
export class VvSearch extends VvInput {

    static styles = [
        VvInput.baseStyles,
        css`
        input[type=search]::-ms-clear,
        input[type=search]::-ms-reveal {
            display: none;
            width: 0;
            height: 0; 
        }
        
        input[type=search]::-webkit-search-decoration,
        input[type=search]::-webkit-search-cancel-button,
        input[type=search]::-webkit-search-results-button,
        input[type=search]::-webkit-search-results-decoration {
            -webkit-appearance:none;
        }
        `,
    ]


    private iconClass : string = 'z-10 absolute right-1 top-1/2 transform -translate-y-1/2 text-vv-grey-dark';

    _renderIcon() {
        return this.value && this.value.length > 0 ? this._renderClose() : this._renderSearch();
    }

    _renderClose() {
        return html`
        <button class="${this.iconClass}" aria-label="Clear Search" @click=${() => this._clearInput()}>
            <vv-icon-close></vv-icon-close>
        </button>
        `
    }

    _renderSearch() {
        return html`
        <span class="${this.iconClass}" aria-hidden="true">
            <vv-icon-search></vv-icon-search>
        </span>`;
    }

    _clearInput() {
        const inputElement = this.shadowRoot!.querySelector('input')!;
        this.value = '';
        inputElement.value = '';
    }

    render() {
        return html`
            <div class="vv-search flex align-center justify-items-center relative w-full" role="search">
                <input 
                    type="search"
                    class="relative w-full"
                    name=${ifDefined(this.name)}
                    id=${ifDefined(this.name)}
                    value=${ifDefined(this.value)}
                    placeholder=${ifDefined(this.placeholder)}
                    autocomplete="search"
                    ?disabled=${this.disabled}
                    @input=${this.handleValueChange} 
                    @change=${this.handleChange}>
                ${this._renderIcon()}
            </div>
        `
    }
}
