import { html, customElement, property, css, CSSResult } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-range-slider')
export class VvRangeSlider extends VvElement {

    static thumbSize: number = 1.25;
    static borderSize: number = 0.125;
    static generateSliderCss(selector: CSSResult, specificAttributesAsCSS: CSSResult): CSSResult {
        return css`input[type=range]::${selector}{
            ${specificAttributesAsCSS}
            height: 0.25rem;
        }
        `;
    }
    static generateThumbsCss(selector: CSSResult, specificAttributesAsCSS: CSSResult): CSSResult {
        return css`input[type=range]::${selector}{
                ${specificAttributesAsCSS}
                height: ${VvRangeSlider.thumbSize}rem;
                width: ${VvRangeSlider.thumbSize}rem;
                cursor: pointer;
                margin-top: ${-1 * VvRangeSlider.thumbSize / 2}rem;
                pointer-events: auto;
                border-radius: 50%;
                border: ${VvRangeSlider.borderSize}rem solid var(--vv-teal);
                background-color: var(--vv-white);
            }
            .wrapper:hover input[type=range]::${selector} {
                background-color: var(--vv-teal);
            }
            .wrapper:hover input[type=range]:disabled::${selector} {
                background-color: var(--vv-grey-mid);
            }
            input[type=range]:disabled::${selector} {
                border: ${VvRangeSlider.borderSize}rem solid var(--vv-grey-mid);
            }
            `;
    }

    static styles = [
        VvElement.baseStyles,
        css`
        *,
        *:before,
        *:after{
            padding: 0;
            margin: 0;
            box-sizing: border-box;
        }
        .container{
            position: relative;
            width: 100%;
        }
        .range-tooltip{
            margin-left: -2%;
        }
        .range-tooltip[disabled]{
            color: var(--vv-grey-dark);
            background-color: var(--vv-grey-mid);
        }
        input[type=range]{
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            width: 100%;
            outline: none;
            position: absolute;
            margin: auto;
            top: 0;
            bottom: 0;
            background-color: transparent;
            pointer-events: none;
        }
        .slider-track{
            width: 98%;
            height: 0.5rem;
            position: absolute;
            margin: auto;
            top: 0;
            left: 1%;
            bottom: 0;
            border-radius: 0.625rem;
        }
        .slider-track[disabled]{
            background-color: var(--vv-grey-light);
        }
        .wrapper:hover .range-tooltip {
            display: block;
        }
        .ticks-shared {
            width: 100%;
            position: relative;
            margin: auto;
            top: 0;
            bottom: 0;
            display: flex;
            justify-content: space-between;
            color: var(--vv-grey-mid);
            padding: 0.125rem 0.625rem;
        }
        .ticks{
            margin-top: .75rem;
        }
        .tick-shared{
            position: relative;
            height: .75rem;
            display: flex;
            justify-content: center;
            width: .25rem;
        }
        .tick-shared.tick.text-tick {
            height: 1rem;
        }
        .tick {
            background: var(--vv-grey-mid);
        }
        .tick-display-text {
            color: var(--vv-black);
        }
        ${VvRangeSlider.generateSliderCss(css`-webkit-slider-runnable-track`, css`-webkit-appearance: none;`)}
        ${VvRangeSlider.generateSliderCss(css`-moz-range-track`, css`-moz-appearance: none;`)}
        ${VvRangeSlider.generateSliderCss(css`-ms-track`, css`appearance: none;`)}


        ${VvRangeSlider.generateThumbsCss(css`-webkit-slider-thumb`, css`-webkit-appearance: none;`)}
        ${VvRangeSlider.generateThumbsCss(css`-moz-range-thumb`, css`-moz-appearance: none;`)}
        ${VvRangeSlider.generateThumbsCss(css`-ms-thumb`, css`appearance: none;`)}
        
        `,
    ]

    @property()
    name?: string;
    @property()
    class?: string;
    @property({ type: Boolean })
    disabled?: boolean;
    @property({ type: Number })
    lowValue: number = 0;
    @property({ type: Number })
    lowValueLimit?: number;
    @property({ type: Number })
    highValue: number = 100;
    @property({ type: Number })
    highValueLimit?: number;
    @property({ type: Number })
    min: number = 0;
    @property({ type: Number })
    max: number = 100;
    @property({ type: Number })
    step: number = 1;
    @property({ type: Boolean })
    lockLowValue?: boolean;
    @property({ type: Boolean })
    lockHighValue?: boolean;
    @property({ type: Array })
    ticks?: { displayText?: string }[]; // pass as empty object for tick only, pass with displayText for text vertically below a tickmark.

    slideOne($event: InputEvent) {
        let input = $event?.target as HTMLInputElement;
        let value = parseInt(input.value || ``);
        if (this.highValue - value <= this.step) {
            this.lowValue = this.highValue - this.step;
            input.value = this.lowValue.toString();
        }
        else if (this.lowValueLimit != undefined && value <= this.lowValueLimit + this.step) {
            this.lowValue = this.lowValueLimit + this.step;
            input.value = this.lowValue.toString();
        }
        else
            this.lowValue = value;
        let customEvent = new CustomEvent(`range-change`, { bubbles: true, composed: true, detail: { lowValue: this.lowValue, highValue: this.highValue, which: `low` } });
        this.handleChange(customEvent);
    }
    slideTwo($event: InputEvent) {
        let input = $event?.target as HTMLInputElement;
        let value = parseInt(input.value || ``);
        if (value - this.lowValue <= this.step) {
            this.highValue = this.lowValue + this.step;
            input.value = this.highValue.toString();
        }
        else if (this.highValueLimit != undefined && value >= this.highValueLimit - this.step) {
            this.highValue = this.highValueLimit - this.step;
            input.value = this.highValue.toString();
        }
        else
            this.highValue = value;
        let customEvent = new CustomEvent(`range-change`, { bubbles: true, composed: true, detail: { lowValue: this.lowValue, highValue: this.highValue, which: `high` } });
        this.handleChange(customEvent);
    }

    handleCustomChange(event: Event) {
        let customEvent = new CustomEvent(event.type, { bubbles: true, composed: true, detail: this.lowValue });
        this.handleChange(customEvent);
    }

    protected handleChange(event: Event) {
        this.dispatchEvent(event);
    }

    _renderTicks() {
        return this.ticks != undefined && this.ticks.length
            ? html`
        <div class="container">
            <div class="ticks-shared ticks">
                ${this.ticks.map((tick) =>
                html`<span class="tick-shared tick ${tick.displayText != undefined ? `text-tick` : ``}"></span>`
            )}
            </div>
        </div>        
        <div class="container">
            <div class="ticks-shared">
                ${this.ticks.map((tick) =>
                html`<span class="tick-shared tick-display-text">${tick.displayText != undefined ? tick.displayText : ``}</span>`
            )}
            </div>
        </div>`
            : html``;
    }

    render() {
        var className = this.class != undefined && this.class != null ? this.class : "";
        const total = this.max - this.min;
        const percent1 = ((1 - ((this.max - this.lowValue) / total)) * 100);
        const percent2 = (1 - ((this.max - this.highValue) / total)) * 100;
        const fillColor = this.disabled ? `var(--vv-teal-light)` : `var(--vv-teal)`;
        const notFilledColor = this.disabled ? `var(--vv-grey-light)` : `var(--vv-grey-mid)`;
        return html`
        <div class="wrapper mt-6 pt-3 pb-2.5 relative ${className}">
            <div class="container">
                <div id="range1" class="range-tooltip min-w-9 -top-8.5 text-center text-vv-white bg-vv-teal absolute hidden text-sm rounded-sm" style="left: ${`${percent1 + ((percent1 >= 50 ? -1 : 1) * (1 - (percent1 / 100)))}%`}" ?disabled=${this.disabled || this.lockLowValue}>
                ${this.lowValue}%
                </div>
                <div id="range2" class="range-tooltip min-w-9 -top-8.5 text-center text-vv-white bg-vv-teal absolute hidden text-sm rounded-sm" style="left: ${`${percent2 + ((percent2 >= 50 ? -1 : 1) * (1 - (percent2 / 100)))}%`}" ?disabled=${this.disabled || this.lockHighValue}>
                ${this.highValue}%
                </div>
            </div>
            <div class="container ">
                <div class="slider-track" style="background: ${`linear-gradient(to right, ${notFilledColor} ${percent1}% , ${fillColor} ${percent1}% , ${fillColor} ${percent2}%, ${notFilledColor} ${percent2}%)`}" ?disabled=${this.disabled}></div>
                <input ?disabled=${this.disabled || this.lockLowValue} type="range" .value="${this.lowValue}" .min="${this.min}" .max="${this.max}" name="${this.name + '-low'}" id="${this.name + '-low'}" step="${this.step}" @input="${this.slideOne}">
                <input ?disabled=${this.disabled || this.lockHighValue} type="range" .value="${this.highValue}" .min="${this.min}" .max="${this.max}" name="${this.name + '-high'}" id="${this.name + '-high'}" step="${this.step}" @input="${this.slideTwo}">
            </div>
            ${this._renderTicks()}
        </div>
        `
    }
}