import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-insights-graph')
export class VvIconInsightsGraph extends VvElement {
    static styles = [
        VvElement.baseStyles,
        css`
    :host{
      width: auto;
      height: fit-content;
    }`,
    ]

    @property({ type: Number })
    size = 200;

    render() {
        return html`
        <svg width=${this.size} height=${this.size} viewBox="0 0 200 200" fill="none" xmlns="http://www.w3.org/2000/svg">
            <rect x="44.6426" y="51.7852" width="7.14286" height="96.4286" rx="1" fill="currentColor"/>
            <rect x="62.5" y="110.714" width="7.14286" height="19.6429" rx="1" fill="currentColor"/>
            <rect x="91.666" y="78.5713" width="7.14286" height="51.7857" rx="1" fill="currentColor"/>
            <rect x="120.833" y="96.4277" width="7.14286" height="33.9286" rx="1" fill="currentColor"/>
            <rect x="150" y="55.3564" width="7.14286" height="75" rx="1" fill="currentColor"/>
            <rect x="44.6426" y="148.214" width="7.14286" height="108.929" rx="1" transform="rotate(-90 44.6426 148.214)" fill="currentColor"/>
        </svg>
`;
    }
}