import { MessageController } from "./MessageController";
import { ReactiveController, ReactiveControllerHost } from "./ReactiveController";
import { QueryCustomerProfilesController } from "./QueryCustomerProfilesController";
import { ControlsController } from "./ControlsController";
import { Context, CustomerProfile } from "../contracts";

export class TwilioBusinessProfileDataController implements ReactiveController {
  constructor(
    private host: ReactiveControllerHost,
    private context: () => Context,
  ) {
    this.host.addController(this);
  }

  private readonly _messageController: MessageController = new MessageController(this.host);
  private readonly _customerProfiles = new QueryCustomerProfilesController(this.host, this._messageController, this.context);
  private readonly _customerProfileControls: ControlsController<CustomerProfile> = new ControlsController(this._messageController, this._customerProfiles.profileQuery);

  hostUpdated() {
  }

  get messages() { 
    return this._messageController;
  }
  get customerProfiles() {
    return this._customerProfiles.profilesQuery.data;
  }
  get customerProfile() {
    return this._customerProfiles.profileQuery.data;
  } 
  get customerProfileControls() {
    return this._customerProfileControls;
  }

  get isProcessing() {
    return this._customerProfiles.profilesQuery.isProcessing
      || this._customerProfiles.profileQuery.isProcessing;
  }
  get isDirty() {
    return this._customerProfiles.profilesQuery.isDirty
      || this._customerProfiles.profileQuery.isDirty;
  }
  get serverHasNewData() {
    return this._customerProfiles.profilesQuery.serverHasNewData
      || this._customerProfiles.profileQuery.serverHasNewData;
  }

  updateCustomerProfile = async () => {
    await this._customerProfiles.updateCustomerProfile();
  }
  submitCustomerProfile = async () => {
    await this._customerProfiles.submitForReview();
  }
  deleteCustomerProfile = async () => {
    await this._customerProfiles.delete();
  }

  clearAndReload = async () => {    
    this._customerProfiles.profileQuery.serverHasNewData = false;
    this._customerProfiles.profileQuery.isDirty = false;
    await this._customerProfiles.reload();
  }

  startNewCustomerProfile = (isStandard: boolean) => {
    this._customerProfiles.startNewCustomerProfile(isStandard);
  }

  selectCustomerProfile = async (sid: string) => {
    await this._customerProfiles.selectCustomerProfile(sid);
  }
}