import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-segmented-control-button')
export class VvSegmentedControlButton extends VvElement {

    connectedCallback() {
        super.connectedCallback();
        this.classList.add('-ml-px', 'border', 'border-solid', 'px-9', 'py-2', 'cursor-pointer', 'text-vv-black', 'hover:bg-vv-grey-light');

        if (this.class) {
            const classes = this.class.split(/[ ]+/);
            this.classList.add(...classes);
        }
    }

    @property()
    class = '';

    @property({ type: Boolean })
    active?: boolean;

    @property({ type : Boolean })
    disabled? : boolean;

    updated(changedProperties : any) {
        if (changedProperties.has('disabled')) {
            const disabledClasses = ['cursor-not-allowed'];
            this.disabled ? this.classList.add(...disabledClasses) : this.classList.remove(...disabledClasses);
        }
        if (changedProperties.has('active')) {
            const activeClasses = ['hover:bg-vv-grey-dark', 'bg-vv-grey-power', 'text-vv-white'];
            if (this.active) {
                this.classList.add(...activeClasses);
                this.setAttribute('aria-pressed', 'true');
            } else {
                this.classList.remove(...activeClasses);
                this.setAttribute('aria-pressed', 'false');
            }
        }
    }

    render() {
        const classList = this.disabled ? 'cursor-not-allowed' : '';
        return html`
            <button class="${classList}">
                <slot></slot>
            </button>
        `
    }
}
