import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-arrow-up')
export class VvIconArrowUp extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ]

  @property({type: Number})
  size = 12;

  render() {
    return html`
    <svg width=${this.size} height=${this.size} viewBox="0 0 ${this.size} ${this.size}" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M5.75 0L10.9462 9H0.553848L5.75 0Z" fill="currentColor"/>
    </svg>`;
  }
}