import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-sidebar-filter')
export class VvSidebarFilter extends VvElement {

    @property()
    header: string = '';

    @property({type: Boolean})
    open? = false;

    @property({type: Boolean})
    hasFilters = false;

    _handleOpenToggle() {
        this.open = !this.open;
    }

    _handleClear() {
        const event = new CustomEvent('filter-cleared', {bubbles: true, composed: true});
        this.dispatchEvent(event);
    }

    render() {
        return html`
            <div class="shadow-md rounded bg-vv-white">
                <div class="flex justify-between p-2 ${this.open ? 'border-b-2 border-gray-200' : ''}">
                    <vv-button type="tertiary" class="inline-flex" @click=${() => this._handleOpenToggle()}> 
                        <vv-selector-button ?open="${this.open}" caret="left">
                            ${this.header}
                        </vv-selector-button>
                    </vv-button>

                    <vv-button type="tertiary" class="${this.hasFilters ? '' : 'hidden'}" @click=${() => this._handleClear()}> 
                        Clear
                    </vv-button>
                </div>
                <div class="transform ease-in-out origin-top duration-200 ${this.open ? 'scale-y-100 p-4' : 'scale-y-0 h-0 invisible'}">
                    <slot></slot>
                </div>
            </div>
        `
    }
}
