import { Context, CampaignUseCase } from "../contracts";
import { ControlsController } from "./ControlsController";
import { MessageController } from "./MessageController";
import { QueryCampaignUseCaseController } from "./QueryCampaignUseCaseController";
import { ReactiveController, ReactiveControllerHost } from "./ReactiveController";

export class TwilioCampaignUseCasesDataController implements ReactiveController {
  constructor(
    private host: ReactiveControllerHost,
    private context: () => Context,
  ) {
    host.addController(this);
  }
  
  private readonly _messageController: MessageController = new MessageController(this.host);
  private readonly _campaignUseCase = new QueryCampaignUseCaseController(this.host, this._messageController, this.context);
  private readonly _campaignUseCaseControls: ControlsController<CampaignUseCase> = new ControlsController(this._messageController, this._campaignUseCase.campaignUseCaseQuery);
  
  hostUpdated() {
  }

  get messages() { 
    return this._messageController;
  }
  get campaignUseCases() {
    return this._campaignUseCase.campaignUseCasesQuery.data;
  }
  get campaignUseCase() {
    return this._campaignUseCase.campaignUseCaseQuery.data;
  }
  get campaignUseCaseControls() {
    return this._campaignUseCaseControls;
  }

  get isProcessing() {
    return this._campaignUseCase.campaignUseCasesQuery.isProcessing
      || this._campaignUseCase.campaignUseCaseQuery.isProcessing;
  }
  get isDirty() {
    return this._campaignUseCase.campaignUseCasesQuery.isDirty
      || this._campaignUseCase.campaignUseCaseQuery.isDirty;
  }
  get serverHasNewData() {
    return this._campaignUseCase.campaignUseCasesQuery.serverHasNewData
      || this._campaignUseCase.campaignUseCaseQuery.serverHasNewData;
  }

  submitCampaignUseCase = async () => {
    return await this._campaignUseCase.submit();
  }
  deleteCampaignUseCase = async () => {
    return await this._campaignUseCase.delete();
  }

  clearAndReload = async () => {
    this._campaignUseCase.campaignUseCaseQuery.serverHasNewData = false;
    this._campaignUseCase.campaignUseCaseQuery.isDirty = false;
    await this._campaignUseCase.reload();
  }

  startNewCampaignUseCase = () => {
    this._campaignUseCase.startNewCampaignUseCase();
  }

  selectCampaignUseCase = async (sid: string) => {
    await this._campaignUseCase.selectCampaignUseCase(sid);
  }
}