import { customElement, html, property, TemplateResult } from "lit-element";
import { TwilioBusinessProfileDataController } from "./Controllers/TwilioBusinessProfileDataController";
import { businessIndustries, businessRegionsOfOperations, businessRegistrationIdTypes, businessTypes, countries, jobPositions, phoneCountries } from "./enums";
import { Context } from "./contracts";
import { BaseElement } from "./BaseElement";

@customElement('twilio-business-profiles')
export class TwilioBusinessProfiles extends BaseElement {
  @property({type: Object})
  context: Context = {} as Context;

  private controller = new TwilioBusinessProfileDataController(this, () => this.context);
  
  private get editingCustomerProfile() { return this.controller.customerProfile; }
  private get input() { return this.controller.customerProfileControls.input; }
  private get select() { return this.controller.customerProfileControls.select; }
  private get multiselect() { return this.controller.customerProfileControls.multiselect; }
  private get radio() { return this.controller.customerProfileControls.radio; }
  private get checkbox() { return this.controller.customerProfileControls.checkbox; }
  private get isProcessing() { return this.controller.isProcessing; }
  private get isDirty() { return this.controller.isDirty; }
  private get disabled() {
    return !!(this.isProcessing || (this.editingCustomerProfile?.status && this.editingCustomerProfile?.status !== "draft" && this.editingCustomerProfile?.status !== "twilio-rejected"));
  }

  hostUpdated() {
  }

  render() {
    return html`
    <div class="container mx-auto pb-8">
      <!-- Hiding business type for now, since Twilio deprecated the "Starter" registration option. -->
      <!-- ${this.businessType()} -->
      ${this.generalInformation()}
      <div class="grid lg:grid-cols-2 gap-12">
        ${this.physicalAddress()}
        ${this.ifStandard(() => this.businessInformation())}
      </div>
      ${this.ifStandard(() => this.authorizedRepresentatives())}
      ${this.buttons()}
    </div>
    `;
  }

  ifTypeSelected(cb: () => TemplateResult) {
  return (this.editingCustomerProfile?.isStandard === true || this.editingCustomerProfile?.isStandard === false) ? cb() : '';
  }
  ifStarter(cb: () => TemplateResult) {
    return (this.editingCustomerProfile && this.editingCustomerProfile.isStandard === false) ? cb() : '';
  }
  ifStandard(cb: () => TemplateResult) {
    return (this.editingCustomerProfile && this.editingCustomerProfile.isStandard === true) ? cb() : '';
  }

  businessType = () => html`
  <hr class="my-4" />
  <h2 class="twiliio-header">Registration Type</h2>
  <div class="grid md:grid-cols-2 gap-12 twilio-trust-hub">
    <div>
      ${this.radio("How many text messages do you plan on sending?", "isStandard", { 
        options: [
          [false, "Starter", "Your organization always sends less than 3,000 text messages per day."],
          [true, "Standard", "Your organization sometimes or frequently sends more than 3,000 text messages per day."],
        ],
        class: "my-4",
        // helperText: "Low-volume customers will only need to submit minimal required information.",
        errorType: "",
        disabled: this.disabled || this.controller?.customerProfile?.mustBeStandard,
        })}
    </div>    
  </div>
  `;

  generalInformation = () => html`
    <hr class="my-4" />
    <h2 class="twiliio-header">General Information</h2>
    <div class="grid md:grid-cols-2 gap-12 twilio-trust-hub">
      <div class="grid md:grid-cols-2 gap-4">
        ${this.input("Business Email", "email", {required: true, class: "col-span-2", errorType: "Information Email Address", disabled: this.disabled })}
        ${this.ifStarter(() => this.generalInformationStarter())}
      </div>
      <div>
        <vv-table .rowClasses=${(row: number) => {
          const sid = this.context.customerProfileSid;
          const hoverClasses = (!sid || ((this.controller?.customerProfiles?.length ?? 0) > 1)) ? "cursor-pointer hover:bg-gray-100" : '';
          const selectedRowClasses = sid === this.controller?.customerProfiles?.[row].sid ? 'bg-gray-100' : '';
          return `${hoverClasses} ${selectedRowClasses}`;
        }}
          .rowClick=${async (row: number) => {
            if (!this.context.customerProfileSid || ((this.controller?.customerProfiles?.length ?? 0) > 1)) { 
              const sid = this.controller?.customerProfiles?.[row].sid ?? "";
              await this.controller.selectCustomerProfile(sid);
            }
          }}
          .cellClasses=${(col: number, row: number) => 
            (col === 0 && this.controller?.customerProfiles?.[row].status === "draft") 
            ? "bg-yellow-100" : ""
          }
          .head=${[
            'Status',
            'Name',
            'Created',
            'Updated',
          ]}
          .body=${this.controller?.customerProfiles?.map(x => [
            x.status,
            x.friendlyName,
            x.dateCreated ? new Date(x.dateCreated).toLocaleString() : '',
            x.dateUpdated ? new Date(x.dateUpdated).toLocaleString() : '',
          ]) ?? []}>
        </vv-table>        
        <div class="mt-4 float-right flex flex-wrap gap-4">
          ${(this.controller?.customerProfiles?.length ?? 0) > 0
            && !this.controller?.customerProfiles?.some(x => x.friendlyName === "Secondary")
            ? html`
            <button class="btn-default" ?disabled=${this.isProcessing}
              @click=${() => {
                if (confirm("Are you sure you want to create a new \"Standard\" Customer Profile?")) {
                  this.controller.startNewCustomerProfile(true);
                }
              }}>
              Create New "Standard" Profile
            </button>
          `: ''}
          ${this.context.isSuperAdmin ? html`
            <button class="btn-default" ?disabled=${this.isProcessing}
            @click=${() => {
              if (confirm("Are you sure you want to create a new Customer Profile?")) {
                this.controller.startNewCustomerProfile(false);
              }
            }}>
              Create New Customer Profile
            </button>
            ${this.editingCustomerProfile?.sid ? html`
              <button class="vv-btn-danger" @click=${this.delete}
                ?disabled=${!this.editingCustomerProfile || this.isDirty || this.isProcessing || !this.editingCustomerProfile.sid}>
                Delete
              </button>
            `: ''}
          ` : ''}
        </div>
      </div>
    </div>
  `;

  statusBackgroundColor() {
    const status = this.editingCustomerProfile?.status;
    return status === "draft" ? "!bg-yellow-100"
      : status === "pending-review" ? "!bg-blue-100"
      : status === "in-review" ? "!bg-blue-100"
      : status === "twilio-rejected" ? "!bg-red-100"
      : status === "twilio-approved" ? "!bg-green-100"
      : "";
  }

  generalInformationStarter = () => html`
    ${this.input("First Name", "firstName", {required: true, errorType: "Information First Name", disabled: this.disabled})}
    ${this.input("Last Name", "lastName", {required: true, errorType: "Information Last Name", disabled: this.disabled})}
    ${this.select("Country ", "countryCode", {required: true, options: phoneCountries, errorType: "", disabled: this.disabled})}
    ${this.input("Phone Number", "phoneNumber", {required: true, helperText: "The phone number must be SMS-enabled.", errorType: "Information Phone Number", disabled: this.disabled})}
  `;

  physicalAddress = () => html`
    <div>
      <hr class="my-4" />
      <h2 class="twiliio-header">Physical Business Address</h2> 
      <vv-alert type="warning" class="block mb-4" .showClose=${false}>
        Currently only physical addresses are accepted. <br />
        P.O. box addresses are not accepted.
      </vv-alert>
      <div class="grid md:grid-cols-2 gap-4">
        ${this.input("Street Address", "streetAddress", {required: true, helperText: "Enter the first line of the business address as it appears in the EIN listing", errorType: "", disabled: this.disabled})}
        ${this.input("Street Address Line 2", "streetAddress2", {helperText: "If applicable, enter the second line of the business address as it appears in the EIN listing", errorType: "", disabled: this.disabled})}
        ${this.input("City", "city", {required: true, helperText: "Enter the city as it appears in the EIN listing", errorType: "", disabled: this.disabled})}
        ${this.input("State/Province/Region", "stateProvinceRegion", {required: true, helperText: "Enter the state/province/region as it appears in the EIN listing", errorType: "", disabled: this.disabled})}
        ${this.input("Postal/Zip Code", "postalZipCode", {required: true, helperText: "Enter the postal code as it appears in the EIN listing", errorType: "", disabled: this.disabled})} 
        ${this.select("Country", "country", {required: true, options: countries, errorType: "", disabled: this.disabled})}
      </div>
    </div>
  `;

  businessInformation = () => html`
    <div>
      <hr class="my-4" />
      <h2 class="twiliio-header">Business Information</h2>
      <div class="grid md:grid-cols-2 gap-4">
        <!-- <div>
          <vv-radio-buttons >
            <vv-radio-button value="direct"></vv-radio-button>
            <vv-radio-button value="isv"></vv-radio-button>
            <vv-radio-button value="other"></vv-radio-button>
          </vv-radio-buttons>
        </div> -->
        ${this.input("Legal Business Name", "legalBusinessName", {required: true, class: 'col-span-2', errorType: "Business Information Business Name", disabled: this.disabled})}
        ${this.select("Business Type", "businessType", {required: true, options: businessTypes, errorType: "Business Information Business Type", disabled: this.disabled})}
        ${this.select("Business Industry", "businessIndustry", {required: true, options: businessIndustries, errorType: "Business Information Industry", disabled: this.disabled})}
        ${this.select("Business Registration ID Type", "businessRegistrationIdType", {required: true, options: businessRegistrationIdTypes, 
          helperText: "We recommend selecting EIN (Employer Identification Number).",
          errorType: "Business Information Business Registration Type", 
          disabled: this.disabled
          })}
        ${this.input("Business Registration Number", "businessRegistrationNumber", {
          required: true, 
          helperText: "Enter the EIN Tax ID as it appears in the EIN listing.",
          errorType: "Business Information Business Registration Number", 
          disabled: this.disabled
          })}
        <div>
        ${this.input("Name of Other Registration Type", "businessRegistrationOtherType", {
          required: true, 
          helperText: "Specify what other registry you will use to identify your business.", 
          class: this.editingCustomerProfile?.businessRegistrationIdType !== 'Other' ? 'hidden' : '', 
          errorType: "Business Information Business Registration Type",
          disabled: this.disabled
          })}
        </div>
        ${this.multiselect("Business Regions of Operations", "businessRegionsOfOperation", {required: true, options: businessRegionsOfOperations, errorType: "Business Information Regions of Operation", disabled: this.disabled})}
        ${this.input("Website URL", "websiteUrl", {required: true, errorType: "Business Information Website URL", disabled: this.disabled})}
        ${this.input("Social Media Profile URL", "socialMediaProfileUrl", {errorType: "Business Information Social Media Profile URL", disabled: this.disabled})}
      </div>
    </div>
  `;

  authorizedRepresentatives = () => html`
    <hr class="my-4" /> 
    <h2 class="twiliio-header">People to Contact</h2>
    <div class="grid grid-cols-1 md:grid-cols-2 gap-12">      
      <p>Your authorized representatives will be contacted to verify your identities.</p>
      <p>Please ensure that they are contactable via email and phone.</p>      
      ${this.checkbox("I do not have a second authorized representative", "doesNotHaveSecondaryContact", {errorType: "", disabled: this.disabled, class: "mb-4" })}
        <!-- <label class="mt-2 block">
          <input type="checkbox" class="mr-2" ?checked=${this.editingCustomerProfile?.doesNotHaveSecondaryContact} 
            @change=${() => {
              if (this.editingCustomerProfile) {
                this.editingCustomerProfile.doesNotHaveSecondaryContact = !this.editingCustomerProfile?.doesNotHaveSecondaryContact;
              }
            }}/>
          I do not have a second authorized representative
        </label> -->
    </div>
    <div class="grid lg:grid-cols-2 gap-12">
      <div>
        <!-- <div class="mt-2">&nbsp;</div> -->
        <h2 class="twiliio-header">Authorized Representative #1</h2>
        <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
          ${this.input("First Name", "primaryContactFirstName", {required: true, errorType: "Authorized Representative #1 First Name", disabled: this.disabled})}
          ${this.input("Last Name", "primaryContactLastName", {required: true, errorType: "Authorized Representative #1 Last Name", disabled: this.disabled})}
          ${this.input("Email", "primaryContactEmail", {required: true, errorType: "Authorized Representative #1 Email Address", disabled: this.disabled})}
          ${this.input("Business Title", "primaryContactBusinessTitle", {required: true, errorType: "Authorized Representative #1 Job Title", disabled: this.disabled})}
          <div class=""><!-- Empty Spacer --></div>
          ${this.select("Job Position", "primaryContactJobPosition", {required: true, options: jobPositions, errorType: "Authorized Representative #1 Job Level", disabled: this.disabled})}
          ${this.select("Country Code", "primaryContactCountryCode", {required: true, options: phoneCountries, errorType: "Authorized Representative #1 Phone Number", disabled: this.disabled})}
          ${this.input("Phone Number", "primaryContactPhoneNumber", {required: true, errorType: "Authorized Representative #1 Phone Number", disabled: this.disabled})} 
        </div>
      </div>
      ${!this.editingCustomerProfile?.doesNotHaveSecondaryContact ? html`
      <div>
        <!-- <label class="mt-2 block">
          <input type="checkbox" class="mr-2" checked />
          I do not have a second authorized representative
        </label> -->
        <h2 class="twiliio-header">Authorized Representative #2</h2>                    
        <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
          ${this.input("First Name", "secondaryContactFirstName", {required: true, errorType: "Authorized Representative #2 First Name", disabled: this.disabled})}
          ${this.input("Last Name", "secondaryContactLastName", {required: true, errorType: "Authorized Representative #2 Last Name", disabled: this.disabled})}
          ${this.input("Email", "secondaryContactEmail", {required: true, errorType: "Authorized Representative #2 Email Address", disabled: this.disabled})}
          ${this.input("Business Title", "secondaryContactBusinessTitle", {required: true, errorType: "Authorized Representative #2 Job Title", disabled: this.disabled})}
          <div class=""><!-- Empty Spacer --></div>
          ${this.select("Job Position", "secondaryContactJobPosition", {required: true, options: jobPositions, errorType: "Authorized Representative #2 Job Level", disabled: this.disabled})}
          ${this.select("Country Code", "secondaryContactCountryCode", {required: true, options: phoneCountries, errorType: "Authorized Representative #2 Phone Number", disabled: this.disabled})}
          ${this.input("Phone Number", "secondaryContactPhoneNumber", {required: true, errorType: "Authorized Representative #2 Phone Number", disabled: this.disabled})}
        </div>
      </div>
      ` : ''}
    </div>
  `;

  buttons = () => html`
    <hr class="mt-4" />
    <div class="mt-4 flex flex-wrap gap-4">
      ${this.isDirty ? html`
        <button class="btn-primary" @click=${() => this.controller?.updateCustomerProfile()}
        ?disabled=${!this.isDirty || this.isProcessing}>
          Save changes
        </button>
      ` : ''}
      ${!this.isDirty && (this.editingCustomerProfile?.status === "draft" || this.editingCustomerProfile?.status === "twilio-rejected") ? html`
        <button class="btn-primary" @click=${this.submitForReview}
          ?disabled=${this.isProcessing}>
          Submit for review
        </button>
      ` : ''}
      ${this.editingCustomerProfile?.status && this.editingCustomerProfile.status !== "draft" && this.editingCustomerProfile.status !== "twilio-rejected" ? html`
        <button class="btn-primary" @click=${() => {
          this.dispatchEvent(new Event('setTabUSA2PBrand'));
          }}
          ?disabled=${this.isDirty || this.isProcessing}>
          Next
        </button>
      ` : ''}
      ${this.controller?.serverHasNewData ? html`
      <span class="bg-red-100 inline-block rounded p-2">
        <!-- TODO - show Alert Icon --> 
        This customer profile was changed on the server. Please 
        <button class="cursor-pointer underline" 
          @click=${this.controller?.clearAndReload}>
          reload
        </button>
        to get the latest information.
      </span>` : ''}
      ${this.controller?.messages.messageComponent('main')}
      ${this.controller?.messages.messageComponent('An error has occured.')}
    </div>
  `;

  submitForReview = async () => {
    if (confirm("Are you sure that you want to submit this Business Profile?\n(You cannot make changes while the submission is being reviewed)")) {
      await this.controller?.submitCustomerProfile();
      // Attempt to navigate to next step if possible.
      if (this.editingCustomerProfile?.status !== "draft" && this.editingCustomerProfile?.status !== "twilio-rejected") {
        this.dispatchEvent(new Event('setTabUSA2PBrand'));
      }
    }
  }

  delete = async () => {
    if (confirm('Are you sure that you want to delete this Business Profile?')) {
      await this.controller?.deleteCustomerProfile();
    }
  }
}

// type CustomerProfileWithoutNestedProperties = Omit<CustomerProfile, 'businessRegionsOfOperation' | 'isStandard' | 'evaluationResults' | 'doesNotHaveSecondaryContact'>;
