import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-empty-state-card')
export class VvEmptyStateCard extends VvElement {

    @property()
    actionType = '';

    @property()
    imageName = '';

    @property()
    title = '';

    @property()
    description = '';

    render() {
        let baseTitle: string = `You haven't created any ${this.actionType} yet`;

        return html`
        <div class="empty-state-card">
            <aside>
                <img class="${this.imageName.toLowerCase()}" src="/AdminSite/content/redesign/EmptyState_200x200/SVG/scorecard.svg" role="presentation" />
                <p id="empty-state-title">
                    ${this.title && this.title.length ? this.title : baseTitle}
                </p>
                <p id="empty-state-description">
                    ${this.description}
                </p>
            </aside>
        </div>
        `
    }
}