import { html, customElement, property, state } from 'lit-element';
import { VvElement } from '../../elements/vv-element';
import { ifDefined } from '../../directives/if-defined';

@customElement('vv-colorpicker')
export class VvColorPicker extends VvElement {
  @property()
  defaultValue = '#ffffff';

  private _value: string = '';
  @property()
  get value(): string {
    return this._value;
  }
  set value(value: string) {
    const oldValue = this._value;
    value = value?.trim()
    const newValue = 
      /^#[0-9abcdef]{3}$/.test(value) || /^#[0-9abcdef]{6}$/.test(value)
      ? value
      : this.defaultValue;
    this._value = newValue;
    this.requestUpdate('value', oldValue);
    if (!this.textboxValue) {
      this.textboxValue = newValue;
    }
  }
  @property()
  name?: string;
  @property()
  label?: string = "Color";
  @property()
  labelClass?: string;
  @property()
  inputClass?: string;
  @property()
  placeholder: string = "#fff...";
  
  private getHiddenColorInput = () => {
    return this.renderRoot.querySelector('input[type=color]') as HTMLInputElement;
  }

  private colorClicked = () => {
    this.getHiddenColorInput().click();
  }
  private hiddenColorChanged = (e: ChangeEvent) => {
    const value = e.target?.value || "";
    this.value = value;
    this.textboxValue = value;
  }

  @state()
  private textboxValue = '';
  private textboxChanged = (e: ChangeEvent) => {
    const value = e.target?.value || "";
    this.value = value;
    this.textboxValue = value;
  }
  private textboxBlurred = () => {
    this.textboxValue = this.value || '';
  }  

  render() {
    return html`
    <label for=${ifDefined(this.name)} class=${ifDefined(this.labelClass)}>${this.label}</label>
    <div class="flex">
      <div class="flex-none">
        <div class="cursor-pointer w-[35px] h-full
          border-2 border-r-0 border-vv-grey-mid rounded-l-sm"
          style="background-color: ${this.value}"
          @click=${this.colorClicked}>
        </div>
      </div>
      <input type="color"
            class="invisible w-[0px]"
            .value=${this.value}
            @input=${this.hiddenColorChanged}
            @change=${this.hiddenColorChanged} 
            />
      <div class="flex-auto">
        <input type="text"
          class="w-full ${this.inputClass}"
          style="border-top-left-radius: 0px !important;
                 border-bottom-left-radius: 0px !important;
                 padding: 8px !important"
          maxlength="7"
          .value=${this.textboxValue}
          @input=${this.textboxChanged}
          @blur=${this.textboxBlurred}
          name=${ifDefined(this.name)}
          placeholder=${this.placeholder} 
          />
      </div>
    </div>
    `;
  }
}