import { html, customElement, property } from "lit-element";
import tippy, { Instance, Placement, Props } from 'tippy.js'
import {roundArrow} from 'tippy.js';
import 'tippy.js/dist/svg-arrow.css';
import 'tippy.js/animations/scale.css';

import { VvElement } from '../../elements/vv-element';

@customElement('vv-tooltip')
export class VvTooltip extends VvElement {
  @property()
  for = "";

  @property()
  contentElementClass = null;

  @property()
  trigger: 'mouseenter' | 'click' = 'mouseenter';

  @property()
  placement: Placement = 'auto';

  @property()
  theme = 'votervoice';

  @property({ type: Number })
  displayDuration? = null;

  @property()
  maxWidth?: string;

  private _tippyInstance? : Instance<Props>;

  connectedCallback() {
    this._initializeTippy();
    this.parentNode?.addEventListener("vv-close-tooltip", () => {
      if (this._tippyInstance) {
        this._tippyInstance.hide();
      }
    });
  }

  disconnectedCallback() {
    if (this._tippyInstance) {
      this._tippyInstance.destroy();
    }
  }

  _initializeTippy() {
    const self = this;
    const targetElement = this._getTargetElement();
    if (!targetElement) throw new Error('Target element not found');

    const displayDuration = this.displayDuration;

    const tippyOptions: Partial<Props> = {
      allowHTML: true,
      animation: 'scale',
      arrow: roundArrow,
      content: this._getContentElement() ?? this.innerHTML,
      interactive: true,
      placement: this.placement,
      theme: this.theme,
      trigger: this.trigger,
      maxWidth: this.maxWidth,
      onShow(instance: { hide: () => void; }): void {
        if (displayDuration) {
          setTimeout(() => {
            instance.hide();
          }, displayDuration);
        }
        self.dispatchEvent(new Event('vv-tooltip-opened'));
      },
      onHide() {
        self.dispatchEvent(new Event('vv-tooltip-closed'));
      },
      onClickOutside() {
        self.dispatchEvent(new Event('vv-tooltip-clicked-outside'));
      },
    };
    
    this._tippyInstance = tippy(targetElement, tippyOptions);
  }

  _getTargetElement() : Element | null | undefined {
    if (this.for) {
      const element = this.parentNode?.querySelector(`#${this.for}`);
      return element;
    }
    
    return this.previousElementSibling || this.nextElementSibling;
  }

  _getContentElement(): Element | null | undefined {
    return this.contentElementClass
      ? this.parentNode?.querySelector(`.${this.contentElementClass}`)
      : null;
  }

  close(): void {
    this._tippyInstance?.hide();
  }

  render() {
    return html`
      <slot></slot>
    `;
  }
}
