import { html, TemplateResult } from "lit-element";
import { ErrorType, MessageController } from "./MessageController";
import { QueryController } from "./QueryController";

export class ControlsController<T extends Record<string, any>> {
  constructor(
    private messageController: MessageController,
    private queryController: QueryController<T>)
  {
  }

  get data() {
    return this.queryController.data;
  }

  label(label: string | TemplateResult, child?: TemplateResult | TemplateResult[], options?: InputOptions) {
    return html`
      <div class=${options?.class || ""}>
        <label class="twilio-label">${label} ${options?.required && html`
          <vv-required-indicator></vv-required-indicator>`}</label>
        ${child}
        ${options?.helperText && html`<p class="helper-text">${options.helperText}</p>`}
        ${options?.errorType && this.messageController.get(options.errorType) && html`
          <p class="helper-text bg-red-100 rounded px-2 py-1">
            ${this.messageController.get(options.errorType)?.message}
          </p>`}
      </div>
    `;
  }

  required(value: string | boolean | number) {
    return !value 
      ? "focus:ring-red-500 focus:border-red-500 border-red-300" 
      : "";
  }

  requiredAtLeastOne(obj: Record<string, any>) {
    return !Object.values(obj || {}).some(isSet => isSet)
      ? "focus:ring-red-500 focus:border-red-500 border-red-300"
      : "";
  }

  update<T>(obj: T | undefined, property: keyof T) {
    return (e: ChangeEvent) => {
      if (obj) {
        obj[property] = (e.target?.value || "") as any;
        this.queryController.isDirty = true;
      } else {
        console.error(`Unable to update ${typeof property === "string" ? property : ""} because data object is not loaded.`);
      }
    };
  }

  input = (label: string | TemplateResult, property: keyof T, options?: InputOptions) => {
    const currentValue = this.data?.[property] ?? "";
    const disabled = options?.disabled || this.queryController.isProcessing || !this.data;
    const child = html`
      <input type="text" class="${options?.required && this.required(currentValue)} ${options?.inputClass ?? ''} w-full" 
        ?disabled=${disabled}
        .value=${currentValue as string}
        @input=${this.update(this.data, property)} />
    `;
    return this.label(label, child, options);
  }

  textarea = (label: string, property: keyof T, options?: InputOptions) => {
    const currentValue = this.data?.[property] ?? "";
    const disabled = options?.disabled || this.queryController.isProcessing || !this.data;
    const child = html`
      <textarea class="${options?.required && this.required(currentValue)} ${options?.inputClass ?? ""} w-full"
        ?disabled=${disabled}
        value=${currentValue as string}
        .value=${currentValue as string}
        @input=${this.update(this.data, property)}
        rows=${5}>
      </textarea>
    `;
    return this.label(label, child, options);
  }

  select = (label: string, property: keyof T, options?: SelectOptions) => {
    const currentValue = this.data?.[property] ?? "";
    const disabled = options?.disabled || this.queryController.isProcessing || !this.data;
    const child = html`
      <select class="select ${options?.required && this.required(currentValue)} w-full"  
        ?disabled=${disabled}
        @change=${this.update(this.data, property)}>
        ${options?.options?.map(o => 
          html`
          <option value=${o[0]}
            .selected=${o[0] === currentValue}
            ?selected=${o[0] === currentValue}>
            ${o[1]}
          </option>`)}
      </select>
    `;
    return this.label(label, child, options);
  }

  checkbox = (label: string, property: keyof T, options?: CheckboxOptions) => {
    const currentValue = !!this.queryController.data?.[property];
    const disabled = options?.disabled || this.queryController.isProcessing || !this.data;
    return html`
    <div class=${options?.class ?? ""}>
      <div class="twilio-label inline-flex">
        <input id=${label} type="checkbox" class="mx-1 ${disabled ? "bg-gray-200" : ""}"
          ?disabled=${disabled}
          ?checked=${currentValue}
          .checked=${currentValue}
          .value=${property as string}
          @change=${() => {
            if (this.data) {
              this.data[property] = !this.data?.[property] as any;
              this.queryController.isDirty = true;
            }
          }}
        /> 
        <label for=${label}>${label}</label>
        ${options?.required && html`<vv-required-indicator></vv-required-indicator>`}
      </div>
      ${options?.helperText && html`<p class="helper-text">${options.helperText}</p>`}
      ${options?.errorType && this.messageController.get(options.errorType) && html`
        <p class="helper-text bg-red-100 rounded px-2 py-1">
          ${this.messageController.get(options.errorType)?.message}
        </p>`}
    </div>
    `;
  }

  multiselect = (label: string, property: keyof T, options?: SelectOptions) => {
    const currentObj: Record<string, any> = this.data?.[property] ?? {};
    const disabled = options?.disabled || this.queryController.isProcessing || !this.data;
    const child = options?.options?.map(([id, display]) => html`
      <div class="my-2">
        <label class="twilio-label inline-flex">
          <input id=${id} type="checkbox" class="mx-1 ${this.requiredAtLeastOne(currentObj)}"
            ?disabled=${disabled}
            .checked=${currentObj?.[id]}
            ?checked=${currentObj?.[id]}
            .value=${id}
            value=${id}
            @change=${() => {
              if (currentObj) {
                currentObj[id] = !currentObj[id];
                this.queryController.isDirty = true;
              }
            }}
          /> 
          <label for=${id}>${display}</label>
        </label>
      </div>
    `);
    return this.label(label, child, options);
  }

  radio = (label: string | TemplateResult, property: keyof T, options?: RadioOptions) => {    
    const currentValue = this.queryController.data?.[property];
    const disabled = options?.disabled || this.queryController.isProcessing || !this.data;
    const child = options?.options?.map(([id, display, description]) => html`
      <div class="my-3">
        <label class="twilio-label inline-flex">
          <input id=${id} type="radio" class="mx-1 appearance-none ${this.required(!!currentValue)}"
            ?disabled=${disabled}
            ?checked=${currentValue === id}
            .checked=${currentValue === id}
            value=${id}
            .value=${id?.toString()}
            @change=${() => {
              if (this.data) {
                this.data[property] = this.data[property] !== id ? id : "" as any;
                this.queryController.isDirty = true;
              }
            }}
          /> 
          <label for=${id} class="w-full">
            ${display} <span class="ml-4">${description}</span>
          </label>
        </label>
      </div>
    `);
    return this.label(label, child, options);
  }
}

export interface InputOptions {
  required?: boolean;
  disabled?: boolean;
  helperText?: string;
  errorType: ErrorType | "";
  class?: string;
  inputClass?: string;
  format?: (value: string) => string;
}

export interface SelectOptions extends InputOptions {
  options: [id: string, display: string][];
}

export interface CheckboxOptions extends InputOptions {
}

export interface RadioOptions extends InputOptions {
  options: [id: string | boolean, display: string, description: string][];
}
