import { html, customElement, property, state } from 'lit-element';
import dayjs from 'dayjs'

import { VvElement } from '../../elements/vv-element';

const TIMEFRAMES = [
    { description: 'All time', value: 'all' },
    { description: 'Last 7 Days', value: '-7 d' },
    { description: 'Last 30 Days', value: '-30 d' },
    { description: 'Last 90 Days', value: '-90 d' },
    { description: 'Last 6 Months', value: '-6 M' },
    { description: 'Last Year', value: '-1 y' },
    { description: 'Last 2 Years', value: '-2 y' },
    { description: 'Custom', value: 'custom' },
];

@customElement('vv-sidebar-daterange-filter')
export class VvSidebarDateRangeFilter extends VvElement {

    constructor() {
        super();
        this.addEventListener('filter-cleared', () => this._resetDates());
        this.addEventListener('start-changed', (e) => this._handleStartChanged(e));
        this.addEventListener('end-changed', (e) => this._handleEndChanged(e));
    }

    @property()
    header: string = '';

    @property({attribute: false})
    startDate : Date | undefined;

    @property({attribute: false})
    endDate : Date | undefined;

    @state()
    _showDatePicker : boolean = false;

    @state()
    _selectedValue : string = 'all';

    _resetDates() {
        this.startDate = undefined;
        this.endDate = undefined;

        this._showDatePicker = false;

        const vvSelect = this.shadowRoot!.querySelector('vv-select')!;
        const select = vvSelect.shadowRoot!.querySelector('select')!;
        select.selectedIndex = 0;

        this._onDateRangeChanged();
    }

    _hasFilters() {
        return this.startDate || this.endDate;
    }

    _handleSelectChanged($event : CustomEvent) {
        // @ts-ignore: value is custom set on vv-select
        const value = $event.target?.value;

        if (value === 'custom') {
            this._showDatePicker = true;
        } else if (value === 'all') {
            this._resetDates();
        } else {
            this._showDatePicker = false;

            const params = value.split(' ');
            const quantity = Number(params[0])
            const unit = params[1];

            this.startDate = dayjs().startOf('day').add(quantity, unit).toDate();
            this.endDate = dayjs().endOf('day').toDate();

            this._onDateRangeChanged();
        }
    }

    _handleStartChanged(e : Event) {
        const $event = e as CustomEvent;
        const value = $event.detail.value;
        this.startDate = dayjs(value).startOf('day').toDate();
        
        this._onDateRangeChanged();
    }

    _handleEndChanged(e : Event) {
        const $event = e as CustomEvent;
        const value = $event.detail.value;
        this.endDate = dayjs(value).endOf('day').toDate();

        this._onDateRangeChanged();
    }

    _onDateRangeChanged() {
        const event = new CustomEvent('daterange-changed', {bubbles: true, composed: true, detail: {
            startDate : this.startDate,
            endDate : this.endDate
        }});
        this.dispatchEvent(event);
    }

    _renderDateRangePicker() {
        const displayedStartDate = this.startDate ? dayjs(this.startDate).startOf('day').format('YYYY-MM-DD') : '';
        const displayedEndDate = this.endDate ? dayjs(this.endDate).endOf('day').format('YYYY-MM-DD') : '';

        return html`
            <div class="pt-4">
                <hr class="mb-4">
                <vv-daterange startDate="${displayedStartDate}" endDate="${displayedEndDate}">
                </vv-daterange>
            </div>`;
    }

    render() {
        return html`
            <vv-sidebar-filter header="${this.header}" ?hasfilters=${this._hasFilters()}>
                <div>
                    <vv-select 
                        class="w-full" 
                        .options="${TIMEFRAMES}" 
                        optionValueProperty="value"
                        optionTextProperty="description"
                        @change=${this._handleSelectChanged}
                    ></vv-select>

                    ${this._showDatePicker ? this._renderDateRangePicker() : html``}
                </div>
            </vv-sidebar-filter>
        `
    }
}
