import { css, customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

@customElement('vv-icon-subtract-small')
export class VvIconSubtractSmall extends VvElement {
  static styles = [
    VvElement.baseStyles,
    css`
    :host{
      width: auto;
      height: fit-content;
    }`,
  ]

  @property({type: Number})
  size = 27;

  render() {
    return html`
    <svg width="${this.size}" height="${this.size}" viewBox="0 0 27 27" fill="currentcolor" xmlns="http://www.w3.org/2000/svg">
      <rect x="6" y="12" width="15" height="2.5" rx="1.25" />
    </svg>    
    `;
  }
}