import { css, html, customElement, property} from 'lit-element';
import { ifDefined } from '../../directives/if-defined';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-button')
export class VvButton extends VvElement {

    static styles = [
        VvElement.baseStyles,
        css`
        :host([type=loading]) > button {
            cursor: not-allowed !important;
        }
        `,
    ];

    @property()
    type?: 'primary' | 'secondary' | 'tertiary' | 'danger' | 'icon' | 'loading' | 'icon-primary' | undefined = undefined;

    @property()
    name?: string;

    @property({type: Boolean})
    disabled?: boolean;
    
    @property()
    class?: string;

    @property()
    buttonClass?: string;

    @property()
    buttonStyle?: string;

    @property({attribute: false})
    onClick?: (e: MouseEvent) => {};

    @property({attribute: false})
    onFocus?: (e: MouseEvent) => {};

    @property({attribute: false})
    onBlur?: (e: MouseEvent) => {};

    // Handle focus state of vv-buttons when an SVG is inside.
    // Maybe could handle another way (retarget the event), but this works for now.
    _onMousedown(e: MouseEvent) {
        this.renderRoot.querySelector("button")?.focus();
        e.preventDefault();
        return true;
    }

    _renderSlot() {
        const slot = this.disabled ? html`<slot class="pointer-events-none"></slot>` : html`<slot></slot>`;
        return this.type === 'loading'
            ? html`<vv-spinner-loader class="px-5"></vv-spinner-loader>`
            : slot;
    }

    render() {
        if (this.disabled) {
            this.setAttribute('aria-disabled', 'true');
        } else {
            this.removeAttribute('aria-disabled');
        }

        const buttonClass = this.class ? this.class : '';
        // vv-btn-primary vv-btn-secondary vv-btn-tertiary vv-btn-danger vv-btn-icon
        const typeClass = this.type === 'loading' ? 'vv-btn-primary flex items-center' : `vv-btn-${this.type?.toLowerCase()} flex items-center`;
        const classes = `${typeClass} ${buttonClass} ${this.buttonClass}`;

        return html`
            <button 
            style=${ifDefined(this.buttonStyle)}
            class=${classes}
            ?disabled=${this.disabled}
            name=${ifDefined(this.name)}
            id=${ifDefined(this.name)}
            @click=${this.onClick}
            @mousedown=${this._onMousedown}
            @focus=${this.onFocus}
            @blur=${this.onBlur}
            >
            ${this._renderSlot()}
            </button>
        `;
    }
}