import { property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

export class VvDateRangeElement extends VvElement {
    
    @property()
    class?: string;

    @property()
    startDate = '';

    @property()
    startLabel : string = '';

    @property()
    startPlaceholder = 'YYYY-MM-DD';

    @property()
    startMin = '';

    @property()
    startMax = '';

    @property()
    endDate = '';

    @property()
    endLabel : string = '';

    @property()
    endPlaceholder = 'YYYY-MM-DD';

    @property()
    endMin = '';

    @property()
    endMax = '';

    @property()
    startDateError : string | undefined = undefined;

    @property()
    endDateError : string | undefined = undefined;

    @property({type: Boolean})
    disabled?: boolean;

    @property({type: Boolean})
    required?: boolean;

    @property()
    options?: any;
}