import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-kpi-block')
export class VvKpiBlock extends VvElement {
    
    @property()
    title : string = '';

    @property()
    subtitle : string = '';

    render() {
        return html`
            <div class="flex flex-col flex-start bg-white shadow-md rounded text-center py-3 px-5" role="listitem">
                <h1 class="font-bold text-2xl uni-neue-800">${this.title}</h1>
                <h2 class="text-vv-grey-dark text-lg">${this.subtitle}</h2>
                <span class="text-vv-grey-dark italic">
                    <slot></slot>
                </span>
            </div>
        `
    }
}
