import { html, customElement } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-card-footer')
export class VvCardFooter extends VvElement {

    render() {
        return html`
            <footer class="p-2.5">
                <slot></slot>
            </footer>
        `
    }
}
