import { html, customElement, property, css } from 'lit-element';
import { VvElement } from '../../elements/vv-element';
import { VvInputElement } from '../../elements/vv-input-element';

@customElement('vv-slider')
export class VvSlider extends VvInputElement {
    static styles = [
        VvElement.baseStyles,
        css`
            *,
            *:before,
            *:after{
                padding: 0;
                margin: 0;
                box-sizing: border-box;
            }
            .wrapper{
                position: relative;
                padding: 50px 40px 20px 40px;
            }
            .container{
                position: relative;
                width: 100%;
            }
            input[type="range"]{
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
                width: 100%;
                outline: none;
                position: absolute;
                margin: auto;
                top: 0;
                bottom: 0;
                background-color: transparent;
                pointer-events: none;
            }
            .slider-track{
                width: 98%;
                height: 12px;
                position: absolute;
                margin: auto;
                top: 0px;
                bottom: 0px;
                left: 1%;
                background-color: var(--vv-grey-mid);
            }
            .slider-track[disabled]{
                background-color: var(--vv-grey-light);
            }
            input[type="range"]::-webkit-slider-runnable-track{
                -webkit-appearance: none;
                height: 5px;
            }
            input[type="range"]::-moz-range-track{
                -moz-appearance: none;
                height: 5px;
            }
            input[type="range"]::-ms-track{
                appearance: none;
                height: 5px;
            }
            input[type="range"]::-webkit-slider-thumb{
                -webkit-appearance: none;
                height: 24px;
                width: 24px;
                cursor: pointer;
                margin-top: -10px;
                pointer-events: auto;
                border-radius: 50%;
                background-color: var(--vv-white);
            }
            input[type="range"]::-moz-range-thumb{
                -webkit-appearance: none;
                height: 24px;
                width: 24px;
                cursor: pointer;
                margin-top: -10px;
                pointer-events: auto;
                border-radius: 50%;
                background-color: var(--vv-white);
            }
            input[type="range"]::-ms-thumb{
                appearance: none;
                height: 24px;
                width: 24px;
                cursor: pointer;
                margin-top: -10px;
                pointer-events: auto;
                border-radius: 50%;
                background-color: var(--vv-white);
            }
            input[type="range"]:not([disabled]):active::-webkit-slider-thumb,
            input[type="range"]:not([disabled]):active::-moz-range-thumb,
            input[type="range"]:not([disabled]):active::-ms-thumb{
                background-color: var(--vv-white);
                border: 3px solid var(--vv-teal-dark);
            }
            #floating-display-value{
                position: absolute;
                color: var(--vv-teal-dark);
                background-color: var(--vv-white);
                top: -44px;
                margin-left: -16px;
                padding: 2px;
                min-width: 32px;
                text-align: center;
                border-radius: 6px;
                border: 1px solid var(--vv-teal-light);
            }
            #floating-display-value[disabled]{
                color: var(--vv-grey-power);
                background-color: var(--vv-grey-light);
                border: none;
            }
        `,
    ]

    @property()
    name?: string;
    @property()
    class?: string;
    @property({ type: Boolean })
    disabled?: boolean;
    @property({ type: Number })
    min: number = 0;
    @property({ type: Number })
    max: number = 100;
    @property({type: Number})
    step:number = 1;

    slider?: any;
    displayValue?: any;
    minGap?: any;
    sliderTrack?: any;
    percentageStyleMoved?: number;


    protected firstUpdated(): void {
        this.slider = this.shadowRoot!.getElementById(`${this.name}`);
        this.slider.value = this.value;
        this.displayValue = this.shadowRoot!.getElementById("floating-display-value");
        this.minGap = 0;
        this.sliderTrack = this.shadowRoot!.querySelector(".slider-track");
        this.setDisplayValues();
    }

    slide() {
        this.slider.value = parseInt(this.slider.value);
        this.value = this.slider.value;
        this.setDisplayValues();
    }
    setDisplayValues(){
        let total = this.max - this.min;
        this.percentageStyleMoved = ((1 - ((this.max - this.slider.value) / total)) * 100);
        this.displayValue.textContent = this.slider.value;
        this.displayValue.style.left = `${this.percentageStyleMoved + ((this.percentageStyleMoved >= 50 ? -1 : 1) * (1 - (this.percentageStyleMoved / 100)))}%`;
    }

    render() {
        return html`
        <div class="wrapper">
            <div class="container">
                <div id="floating-display-value" ?disabled=${this.disabled}>
                </div>
            </div>
            <div class="container">
                <div class="slider-track" ?disabled=${this.disabled}></div>
                <input ?disabled=${this.disabled} type="range" min="${this.min}" max="${this.max}" name="${this.name}" id="${this.name}" step="${this.step}" @input="${this.slide}">
            </div>
        </div>
        `
    }
}