import { html, customElement, property} from 'lit-element';
import { VvDateRangeElement } from './vv-daterange-element';
import { ifDefined } from '../../directives/if-defined';

import './vv-daterange-simple';
import './vv-daterange-fancy';

@customElement('vv-daterange')
export class VvDateRange extends VvDateRangeElement {

    @property()
    type : 'simple' | 'fancy' = 'simple';

    _onDateRangeChanged($event : CustomEvent) {
        const e = new CustomEvent('daterange-changed', { bubbles: true, composed: true, detail: $event.detail });

        this.dispatchEvent(e);
    }

    render() {
        return this.type === 'simple' 
        ? html`
            <vv-daterange-simple
                class=${ifDefined(this.class)}
                startDate="${ifDefined(this.startDate)}"
                startLabel="${ifDefined(this.startLabel)}"
                startPlaceholder="${ifDefined(this.startPlaceholder)}"
                startMin="${ifDefined(this.startMin)}"
                startMax="${ifDefined(this.startMax)}"
                startDateError="${ifDefined(this.startDateError)}"
                endDate="${ifDefined(this.endDate)}"
                endLabel="${ifDefined(this.endLabel)}"
                endPlaceholder="${ifDefined(this.endPlaceholder)}"
                endMin="${ifDefined(this.endMin)}"
                endMax="${ifDefined(this.endMax)}"
                endDateError="${ifDefined(this.endDateError)}"              
                ?disabled=${this.disabled}
                ?required=${this.required}>
            </vv-daterange-simple>    
        `
        : html`
            <vv-daterange-fancy
                class=${ifDefined(this.class)}
                .options=${ifDefined(this.options)}
                startDate="${ifDefined(this.startDate)}"
                endDate="${ifDefined(this.endDate)}"
                @daterange-changed=${(e:CustomEvent) => this._onDateRangeChanged(e)}>
            <vv-daterange-fancy>
        `
    }
}
