import { css, html, customElement, property} from 'lit-element';
import { VvElement } from '../../elements/vv-element';
import { ifDefined } from '../../directives/if-defined';

import { DuetDatePicker } from '@duetds/date-picker/custom-element';

customElements.define('duet-date-picker', DuetDatePicker);

@customElement('vv-datepicker')
export class VvDatePicker extends VvElement {
    // See https://github.com/duetds/date-picker
    static vvDatePickerStyles = css`
        :host {
            --duet-color-primary: var(--vv-teal);
            --duet-color-text: var(--vv-black);
            --duet-color-text-active: var(--vv-white);
            --duet-color-placeholder: var(--vv-grey-power);
            --duet-color-button: var(--vv-grey-light);
            --duet-color-surface: var(--vv-white);
            --duet-color-overlay: rgba(0, 0, 0, 0.8);
            --duet-color-border: var(--vv-grey-mid);

            --duet-font: 'uni-neue';
            --duet-font-normal: 400;
            --duet-font-bold: 600;

            --duet-radius: 3px;
            --duet-z-index: 600;
        }

        :host([error]) {
            --duet-color-border: var(--vv-red-fire);
        }

        .duet-date__input:disabled {
            background: var(--vv-grey-light);
        }
    `
    static styles = [VvElement.baseStyles, VvDatePicker.vvDatePickerStyles];

    @property()
    value = '';

    @property()
    label : string = '';

    @property()
    placeholder = 'YYYY-MM-DD';

    @property()
    min = '';

    @property()
    max = '';

    @property()
    error = '';

    @property({type: Boolean})
    disabled?: boolean;

    @property({type: Boolean})
    required?: boolean;

    // Overriding localization requires full object
    private _localization : any = {
        buttonLabel: 'Choose date',
        placeholder: this.placeholder,
        selectedDateMessage: 'Selected date is',
        prevMonthLabel: 'Previous month',
        nextMonthLabel: 'Next month',
        monthSelectLabel: 'Month',
        yearSelectLabel: 'Year',
        closeLabel: 'Close window',
        calendarHeading: 'Choose a date',
        dayNames: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
        monthNames: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
        monthNamesShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
    };

    render() {
        return html`
            <label>
                ${this.label}
                <duet-date-picker
                    value="${ifDefined(this.value)}" 
                    min="${ifDefined(this.min)}"
                    max="${ifDefined(this.max)}"
                    first-day-of-week="0"
                    .localization=${this._localization}
                    ?disabled=${this.disabled}
                    ?required=${this.required}
                >
                </duet-date-picker>
                <span class="text-vv-red-fire">${this.error}</span>
            </label>
        `
    }
}
