import { html, customElement, property } from 'lit-element';
import copy from 'copy-to-clipboard';

import { VvElement } from '../../elements/vv-element';

@customElement('vv-clipboard-copy')
export class VvClipboardCopy extends VvElement {

    @property()
    value = '';

    @property({type: Number})
    size = 40;

    _onClick() {
        copy(this.value);
        const event = new CustomEvent('copied', {bubbles: true, composed: true});
        this.dispatchEvent(event);
    }

    render() {
        return html`
        <div class="flex">
            <input type="text" onclick="this.select()" class="text-sm" style="border-top-right-radius: 0px !important;border-bottom-right-radius: 0px !important" 
                value="${this.value}" size="${this.size}" aria-label="${this.value}" readonly />
            <button class="px-4 border-vv-grey-mid border-t-2 border-b-2 border-r-2 hover:bg-vv-grey-light active:bg-vv-grey-mid focus:bg-vv-grey-light"
                style="border-top-right-radius: 3px !important;border-bottom-right-radius: 3px !important"
                @click=${this._onClick}
                aria-label="Copy to clipboard">
                <vv-icon-clone size="16"></vv-icon-clone>
            </button>
            <vv-tooltip trigger="click" displayDuration="2000">  
                <div class="px-4 py-2">Copied!</div>
            </vv-tooltip>
        </div>
        `
    }
}