import { customElement, html, property } from "lit-element";
import { TwilioUSA2PBrandsDataController } from "./Controllers/TwilioUSA2PBrandsDataController";
import { companyTypes, Enum, stockExchanges } from "./enums"
import { Context } from "./contracts";
import { BaseElement } from "./BaseElement";

@customElement('twilio-us-a2p-brands')
export class TwilioUSA2PBrands extends BaseElement {
  @property({type: Object})
  context: Context = {} as Context;

  private readonly controller = new TwilioUSA2PBrandsDataController(this, () => this.context);
  private companyTypeOptions = () => this.context.isStandardCustomerProfile 
    ? companyTypes : [ [ "starter", "starter" ] ] as Enum;

  hostUpdated() {    
  }

  render() {
    const data = this.controller?.usA2PBrand;
    const input = this.controller?.usA2PBrandControls.input;
    const select = this.controller?.usA2PBrandControls.select;
    const checkbox = this.controller?.usA2PBrandControls.checkbox;
    const disabled = !!(data?.status && data?.status !== 'draft' && data?.brandRegistrationStatus !== 'FAILED') || this.controller?.isProcessing;

    return html`
      <div class="container mx-auto pb-8">
        <hr class="my-4" />
        <div>
          Tells us a bit more about your business, so that we can give you a 
          <!-- <a href="https://www.campaignregistry.com/" target="_blank" class="underline"> -->
            TCR (The Campaign Registry)
          <!-- </a> -->
          Score used by carriers to determine your messaging limits.
          <!-- <vv-tooltip>
            <span class="bold underline">TCR</span>
            <span slot="tooltiptext" style="width: 200px">
              <h1 class="bold">What is a TCR Trust Score?</h1>
              <p>
                A Trust Score is a score assigned by The Campaign Registry
                (TCR - a 3rd party that administers the wireless carrier's new registration system). 
                TCR uses a reputation algorithm to review specific criteria relating to your company, and then assigns a Trust Score. 
                The carriers use this score to represent how trusted your business is, and determine your daily messaging limits and throughput.
                The higher your Trust Score, the higher your daily messaging limits and throughput.
              </p>
            </span>
          </vv-tooltip> -->
        </div>

        <hr class="my-4" />
        <div class="grid lg:grid-cols-2 gap-4">
          <div class="grid md:grid-cols-2 gap-4 auto-rows-max">
            ${select("Company Type", "companyType", {
              options: this.companyTypeOptions(), required: true, disabled, errorType: "",
            })}
            ${data?.companyType === "public" ? select("Stock Exchange", "stockExchange", {
              options: stockExchanges, required: true, disabled, errorType: "",
            }) : ''}
            ${data?.companyType === "public" ? input("Stock Ticker", "stockTicker", {
              required: true, disabled, errorType: "",
            }) : ''}
            ${((data?.brandScore ?? 0) > 0) 
              ? input("Brand Score", "brandScore", {disabled: true, errorType: ''}) : ''}
            ${data?.tcrId 
              ? input("TCR ID", "tcrId", {disabled: true, errorType: ''}) : ''}
            ${data?.failureReason 
              ? input("Failure Reason", "failureReason", {disabled: true, errorType: ''}) : ''}              
            ${this.controller.usA2PBrand?.mock 
              ? checkbox('Is "mock" brand', "mock", {disabled: true, errorType: ''}) : ''}
          </div>
          <div>
            <vv-table .rowClasses=${(i: number) => {
              const sid = this.context.trustProductSid;
              const hoverClasses = (!sid || ((this.controller.usA2PBrands?.length ?? 0) > 1)) ? "cursor-pointer hover:bg-gray-100" : "";
              const selectedRowClasses = sid === this.controller.usA2PBrands?.[i]?.sid ? "bg-gray-100" : "";
              return `${hoverClasses} ${selectedRowClasses}`;
            }}
              .rowClick=${async (i: number) => {
                if (!this.context.trustProductSid || ((this.controller.usA2PBrands?.length ?? 0) > 1)) {
                  const sid = this.controller.usA2PBrands?.[i]?.sid ?? "";
                  await this.controller.select(sid);
                }
              }}
              .cellClasses=${(col: number, row: number) => 
                (col === 0 && this.controller?.usA2PBrands?.[row].status === "draft") 
                ? "bg-yellow-100" : ""
              }
              .head=${[
                "Status",
                "TCR Status",
                "Created",
                "Updated",
              ]}
              .body=${this.controller?.usA2PBrands?.map(x => [
                  x.status ?? '',
                  x.brandRegistrationStatus ?? '',
                  x.dateCreated ? new Date(x.dateCreated).toLocaleString() : '',
                  x.dateUpdated ? new Date(x.dateUpdated).toLocaleString() : '',
                ]) ?? []
              }>
            </vv-table>
            ${this.context.isSuperAdmin ? html`
              <div class="mt-4 float-right flex flex-wrap gap-4">
                <button class="btn-default" 
                  ?disabled=${this.controller.isProcessing}
                  @click=${this.createNew}>
                  Create New US A2P Brand
                </button>
                ${this.controller.usA2PBrand?.sid && html`
                  <button class="vv-btn-danger" 
                    @click=${this.delete}
                    ?disabled=${this.controller.isProcessing}>
                    Delete
                  </button>
                `}
              </div>
            ` : ''}
          </div>
        </div>
        
        <hr class="mt-4" />
        <div class="mt-4 flex flex-wrap gap-4">
          ${!disabled ? html`
            ${this.controller?.isDirty || data?.brandRegistrationStatus === "FAILED" ? html`
              <button class="btn-primary" 
                @click=${this.controller?.updateBrand}>
                Save changes
              </button>
            ` : ''}
            ${this.controller.usA2PBrand?.sid && !this.controller?.isDirty && data?.brandRegistrationStatus !== "FAILED" ? html`
              <button class="btn-primary" 
                @click=${() => this.submit(false)}
                ?disabled=${disabled}>
                Submit for review
              </button>
              ${this.context.isSuperAdmin ? html`
                <button class="btn-default" 
                  @click=${() => this.submit(true)}
                  ?disabled=${disabled}>
                  Submit "Mock" (for testing)
                </button>              
              ` : ''}            
            `: ''} 
          ` : html`
            <button class="btn-primary" @click=${() => this.dispatchEvent(new Event('setTabCampaignUseCases'))}
              ?disabled=${this.controller.isDirty || this.controller.isProcessing}>
              Next
            </button>
          `}         
          ${this.controller?.messages?.messageComponent()}
        </div>
      </div>
    `;
  }

  private submit = async (isMockBrand: boolean) => {
    if (confirm("Are you sure that you want to submit this brand?\n(You cannot make changes while the submission is being reviewed)")) {
      await this.controller?.submitBrand(isMockBrand);
      // Attempt to navigate to next step if possible.
      const data = this.controller?.usA2PBrand;
      const canNavigate = (data?.status && data?.status !== 'draft' && data?.brandRegistrationStatus !== 'FAILED' && !this.controller?.isDirty);
      if (canNavigate) {
        this.dispatchEvent(new Event('setTabCampaignUseCases'));
      }
    }
  }

  private createNew = () => {
    if (confirm("Are you sure you want to create a new US A2P Brand?")) {
      this.controller.startNew();
    }
  }

  private delete = async () => {
    if (confirm("Are you sure that you want to delete this brand?")) {
      await this.controller?.deleteBrand();
    }
  }
}