import { Context, CustomerProfile, EvaluationContract } from "../contracts";
import { MessageController } from "./MessageController";
import { QueryController } from "./QueryController";
import { ReactiveControllerHost } from "./ReactiveController";

export class QueryCustomerProfilesController {
  constructor(
    private host: ReactiveControllerHost,
    private messageController: MessageController,
    private context: () => Context,
  ) {
    this.profilesQuery = new QueryController(this.host, this.messageController, this.context, this.getCustomerProfiles);
    this.profileQuery = new QueryController(this.host, this.messageController, this.context, this.getCustomerProfile);
  }

  public readonly profilesQuery: QueryController<CustomerProfile[]>; 
  public readonly profileQuery: QueryController<CustomerProfile>;

  reload = async (isProcessing?: boolean) => {
    await this.profilesQuery.reload(isProcessing);
    await this.profileQuery.reload(isProcessing);
  }

  startNewCustomerProfile = (isStandard: boolean) => {
    this.messageController.clear();
    this.profileQuery.data = {
      isStandard: isStandard,
      mustBeStandard: isStandard,
      businessRegionsOfOperation: {},
      doesNotHaveSecondaryContact: false,
    } as CustomerProfile;
    this.profileQuery.serverHasNewData = false;
    this.profileQuery.isDirty = true;
    this.context().customerProfileSid = "";
    this.context().trustProductSid = "";
    this.context().campaignUseCaseSid = "";
    this.host.requestUpdate();
  }

  selectCustomerProfile = async (sid: string) => {
    this.messageController.clear();
    this.profileQuery.data = undefined;
    this.profileQuery.serverHasNewData = false;
    this.profileQuery.isDirty = false;
    this.context().customerProfileSid = sid;
    this.context().trustProductSid = "";
    this.context().campaignUseCaseSid = "";
    await this.reload(true);
  }

  getCustomerProfiles = async () => {
    let cps = await this.profilesQuery.getJson<CustomerProfile[]>(`/CustomerProfiles?associationId=${this.context().associationId}`);
    // Remove the "Starter" profiles, since they have been deprecated.
    cps = cps.filter(x => x.isStandard);
    if (cps && !this.profileQuery.isDirty) {
      const customerProfileSid = this.context().customerProfileSid;
      const customerProfile = this.profileQuery.data;
      const noCustomerProfileToLoad = !customerProfileSid && !customerProfile;  
      const hasAtLeastOne = (noCustomerProfileToLoad && cps?.length); // Pick the first CustomerProfile.
      // The CustomerProfile doesn't seem to exist anymore.
      const doesNotExistAnymore = (customerProfile?.sid && !cps?.some(x => x.sid === customerProfile?.sid)); 
      const firstSid = cps?.[0]?.sid ?? "";
      if ((hasAtLeastOne || doesNotExistAnymore) && customerProfileSid !== firstSid) {
        await this.selectCustomerProfile(firstSid);
      } else if (noCustomerProfileToLoad) {
        // Setting to "true" so that we will create "Standard" registrations from now on.
        // This is because Twilio deprecated the "Starter" registrations.
        this.startNewCustomerProfile(true); 
      }
    }
    return cps;
  }

  getCustomerProfile = async () => {
    if (!this.context().customerProfileSid) {
      return this.profileQuery.data;
    }
    const cp = await this.profileQuery.getJson<CustomerProfile>(`/CustomerProfiles/${this.context().customerProfileSid}?associationId=${this.context().associationId}`);
    if (cp?.businessRegionsOfOperation) {
      const businessRegionsOfOperationCapitalized: Record<string, boolean> = {};
      for (const key of Object.keys(cp.businessRegionsOfOperation)) {
        businessRegionsOfOperationCapitalized[key.toUpperCase()] = cp.businessRegionsOfOperation[key];
      }
      cp.businessRegionsOfOperation = businessRegionsOfOperationCapitalized;
    }
    this.context().isStandardCustomerProfile = cp.isStandard;
    return cp;
  }

  updateCustomerProfile = async () => {
    if (!this.profileQuery.data) {
      this.messageController.clearAndSet("Customer Profile not loaded yet.", 'main');
      return;
    }
    const results = await this.profileQuery.postJson<EvaluationContract>(`/CustomerProfiles/${this.profileQuery.data.sid ?? '_'}?associationId=${this.context().associationId}`, this.profileQuery.data);
    this.profileQuery.isDirty = false;
    this.messageController.clearAndSet("Saved!", "main", 5000);
    if (results.status === 'noncompliant') {
      this.messageController.addValidationErrors(results);
    }
    await this.selectCustomerProfile(results.customer_profile_sid);
  }

  submitForReview = async () => {
    if (!this.profileQuery.data) {
      this.messageController.clearAndSet("Customer Profile not loaded yet.", 'main');
      return;
    }
    const results = await this.profileQuery.postJson<EvaluationContract>(`/CustomerProfiles/${this.profileQuery.data.sid}/submit?associationId=${this.context().associationId}`, {});
    this.profileQuery.isDirty = false
    this.messageController.clearAndSet("Submitted!", "main", 5000);
    if (results.status === 'noncompliant') {
      this.messageController.addValidationErrors(results);
    }
    await this.reload(true);
  }

  delete = async () => {
    if (!this.profileQuery.data) {
      this.messageController.clearAndSet("Customer Profile not loaded yet.", 'main');
      return;
    }
    const success = await this.profileQuery.deleteJson<boolean>(`/CustomerProfiles/${this.profileQuery.data.sid}?associationId=${this.context().associationId}`, {});
    if (!success) {
      this.messageController.clearAndSet("Unable to delete Customer Profile.");
    } else {
      this.messageController.clearAndSet("Deleted!", "main", 5000);
      this.profileQuery.serverHasNewData = false;
      this.profileQuery.isDirty = false;
    }
    await this.selectCustomerProfile("");
  }
}