import { html, customElement, property } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-pill')
export class VvPill extends VvElement {
    static styles = [
        VvElement.baseStyles
    ]

    @property({ type: Boolean })
    disabled?: boolean;

    @property()
    icon?: string;

    @property()
    type?: string;

    @property()
    maxInnerWidth?: number;

    private _renderPillTypeClass(type?: string): string {
        if (!type || type.trim() === '') return ``;

        const primaryClasses = `bg-vv-teal text-vv-white border-transparent hover:bg-vv-teal-dark`;
        const primaryDisabledClasses = `bg-vv-teal-lightest border-transparent`;
        const secondaryClasses = `bg-vv-white border-vv-teal text-vv-black hover:bg-vv-teal-lightest`;
        const secondaryDisabledClasses = `bg-vv-grey-light border-vv-teal-lightest text-vv-grey-dark`;
        const fnClasses = `bg-fn-purple text-vv-white hover:bg-fn-purple-dark`;
        const fnDisabledClasses = `bg-vv-grey-light text-vv-grey-dark border-fn-purple`;
        const allClasses = `${primaryClasses} ${primaryDisabledClasses} ${secondaryClasses} ${secondaryDisabledClasses} ${fnClasses} ${fnDisabledClasses}`;

        switch (type) {
            case `primary`:
                switch (this.disabled) {
                    case true:
                        return primaryDisabledClasses; //primary & disabled
                    default:
                        return primaryClasses;  //primary & not disabled
                }
            case `secondary`:
                switch (this.disabled) {
                    case true:
                        return secondaryDisabledClasses; //secondary & disabled
                    default:
                        return secondaryClasses;  //secondary & not disabled
                }
            case `fn`:
                switch (this.disabled) {
                    case true:
                        return fnDisabledClasses; //fn & disabled
                    default:
                        return fnClasses;  //fn & not disabled
                }
            case `all`:
                return allClasses;
            default:
                return ``;
        }
    }

    private _renderIconTypeClass() {
        if (this.type === 'secondary' && !this.disabled)
            return `text-vv-grey-dark`;
        else
            return ``;
    }

    private _renderIcon() {
        if (!this.icon) return '';

        switch (this.icon) {
            case 'close':
                return html`<vv-icon-close size="16" class="inline-block pl-0.5 -mt-1.5 ${this._renderIconTypeClass()}"></vv-icon-close>`;
            case 'add':
                return html`<vv-icon-add size="18" class="inline-block pl-0.5 -mt-1.5 ${this._renderIconTypeClass()}"></vv-icon-add>`;
            default:
                return ``;
        }
    }

    render() {
        let basePillStyles = [`py-1`, `px-2`, `mr-2`, `my-1`, `inline-block`, `rounded-2xl`, `border`];
        this.classList.remove(...this._renderPillTypeClass(`all`).split(` `), `cursor-pointer`);
        if (!this.disabled)
            basePillStyles.push(`cursor-pointer`);
        this.classList.add(...basePillStyles, ...this._renderPillTypeClass(this.type).split(` `));
        return html`
            <style>
                .vv-pill-inner {
                    max-width: ${this.maxInnerWidth ? `${this.maxInnerWidth}` : `none`};
                }                
            </style>
            <span ?disabled=${this.disabled}>
                <span class="vv-pill-inner vv-ellipsis">
                    <slot></slot>
                </span>
                ${this._renderIcon()}
            </span>
        `
    }
}
