import { html, TemplateResult } from "lit-element";
import { EvaluationContract } from "../contracts";
import { ReactiveController, ReactiveControllerHost } from "./ReactiveController";

export class MessageController implements ReactiveController {
  constructor(
    private host: ReactiveControllerHost
  ) {
    host.addController(this);
  }

  hostConnected(){
  }

  private messages: Map<string, Message> = new Map<string, Message>();
  get(type: string) {
    return this.messages.get(type);
  }
  set(message: string, type?: string, timeoutMs?: number) {   
    const key = type ?? message;
    let msg = this.messages.get(key);
    if (msg) {
      msg.message = message;
      msg.type = type;
      msg.timeoutMs = timeoutMs;
    } else {
      msg = {
        message,
        type,
        timeoutMs,
      };
      this.messages.set(key, msg);
    }
    if (msg.timeoutHandle) {
      clearTimeout(msg.timeoutHandle);
      msg.timeoutHandle = undefined;
    }
    if (timeoutMs) {
      msg.timeoutHandle = setTimeout(() => {
        if (this.messages.delete(key)) {
          this.host.requestUpdate();
        }
      }, timeoutMs) as unknown as number;
    }
    this.host.requestUpdate();
  }
  clear(type?: string) {
    if (type) {
      const msg = this.messages.get(type);
      if (msg) {
        msg.timeoutHandle && clearTimeout(msg.timeoutHandle);
        this.messages.delete(type);
        this.host.requestUpdate();
      }
    } else if (this.messages.size > 0) {
      this.messages.forEach(m => m.timeoutHandle && clearTimeout(m.timeoutHandle));
      this.messages.clear();
      this.host.requestUpdate();
    }
  }
  clearAndSet(message: string, type?: string, timeoutMs?: number) {
    this.clear();
    this.set(message, type, timeoutMs);
  }

  messageComponent(type: string = '') {
    if (type) {
      const message = this.get(type);
      if (message) {
        const color = message.message === 'Saved!' || message.message === 'Submitted!' ? 'bg-green-100' : 'bg-red-100';
        return html`
        <span class="${color} inline-block rounded p-2">
          ${message?.message}
        </span>`;
      } else {
        return '';
      }
    } else {
      const components: (TemplateResult|string)[] = [];
      for (const [type, _] of this.messages) {
        if (type) {
          components.push(this.messageComponent(type));
        }
      }
      return html`${components}`;
    }
  }

  addValidationErrors(e: EvaluationContract) {
    if (e.status === 'noncompliant') {
      this.clearAndSet("There are validation errors with this application.", 'main');
      for (const r of e.results) {
        if (!r.passed) {
          for (const f of r.fields) {
            if (!f.passed) {
              this.set(f.failure_reason, `${r.friendly_name} ${f.friendly_name}`);
            }
          }
        }
      }
    }
  }
}

export interface Message {
  message: string;
  type?: string;
  timeoutMs?: number;
  timeoutHandle?: number;
}

export type ErrorType =
| "Information Email Address"
| "Information First Name"
| "Information Last Name"
| "Information Phone Number"
| "Legal Company Address Street Address"
| "Business Information Business Name"
| "Business Information Business Type"
| "Business Information Industry"
| "Business Information Business Registration Type"
| "Business Information Business Registration Number"
| "Business Information Business Identity"
| "Business Information Website URL"
| "Business Information Regions of Operation"
| "Business Information Social Media Profile URL"
| "Physical Business Address address sids"
| "Authorized Representative #1 First Name"
| "Authorized Representative #1 Last Name"
| "Authorized Representative #1 Email Address"
| "Authorized Representative #1 Phone Number"
| "Authorized Representative #1 Job Title"
