import { html, customElement, property, state } from 'lit-element';
import { VvElement } from '../../elements/vv-element';

@customElement('vv-pagination')
export class VvPagination extends VvElement {

    connectedCallback() {
        super.connectedCallback();
        this.classList.add('flex');
        this.classList.add('justify-center');
        this.classList.add('pt-4');
    }

    @property({ type: Number })
    totalPages: number = 0;

    @property({ type: Number })
    paginationSize: number = 4;

    @property({ type: Number, reflect: true })
    currentPage: number = 1; // 1 based

    @state()
    displayedPages: Array<DisplayPage> = [];

    private _handleFirstPageClicked() {
        this.currentPage = 1;
        this._raisePageChangedEvent();
    }

    private _handlePreviousPageClicked() {
        if (this.currentPage > 1) {
            this.currentPage--;
            this._raisePageChangedEvent();
        }
    }

    private _handleNextPageClicked() {
        if (this.currentPage < this.totalPages) {
            this.currentPage++;
            this._raisePageChangedEvent();
        }
    }

    private _handleLastPageClicked() {
        this.currentPage = this.totalPages;
        this._raisePageChangedEvent();
    }

    private _handlePageClicked(pageNumber: number) {
        if (pageNumber < 1 || pageNumber > this.totalPages || pageNumber === this.currentPage) return;

        this.currentPage = pageNumber;
        this._raisePageChangedEvent();
    }

    private _raisePageChangedEvent() {
        const event = new CustomEvent('page-changed', {bubbles: true, composed: true, detail: {
            currentPage: this.currentPage
        }});
        this.dispatchEvent(event);
    }

    private _setDisplayedPages() {
        let displayedPages: Array<DisplayPage> = [];

        const totalSections = Math.ceil(this.totalPages / this.paginationSize);
        const activeSection = Math.ceil(this.currentPage / this.paginationSize);
        const activeSectionFirstPage = ((activeSection - 1) * this.paginationSize) + 1;
        const activeSectionLastPage = activeSectionFirstPage + this.paginationSize;

        if (activeSection > 1) {
            displayedPages.push(new DisplayPage(activeSectionFirstPage - 1, '...'));
        }

        for (let i = activeSectionFirstPage; i < activeSectionLastPage; i++) {
            if (i > this.totalPages) {
                break;
            }

            displayedPages.push(new DisplayPage(i, i.toString()));
        }

        if (activeSection < totalSections) {
            displayedPages.push(new DisplayPage(activeSectionLastPage, '...'));
        }

        this.displayedPages = displayedPages;
    }

    updated(changedProperties: Map<string, unknown>) {
        if (changedProperties.has('totalPages') || changedProperties.has('paginationSize') || changedProperties.has('currentPage')) {
            this._setDisplayedPages();
        }
    }

    private _renderFirstPageLink() {
        return this.currentPage > this.paginationSize ? html`
        <li>
            <a aria-label="First Page" @click=${() => this._handleFirstPageClicked()}
                class="${this.currentPage === 1 ? 'active' : ''}">
                1
            </a>
        </li>
        ` : html``;
    }

    private _renderDisplayedPage(page: DisplayPage) {
        return page.display !== '...' || (page.display === '...' && page.value !== this.totalPages) ? html`
        <li>
            <a @click=${() => this._handlePageClicked(page.value)}
                class="${this.currentPage === page.value ? 'active' : ''}">
                ${page.display}
            </a>
        </li>
        ` : html``;
    }

    private _renderLastPageLink() {
        const canRender = this.currentPage !== this.totalPages && this.displayedPages.length > 0 && this.displayedPages[this.displayedPages.length - 1].display !== this.totalPages.toString();
        return canRender ? html`
        <li>
            <a aria-label="First Page" @click=${() => this._handleLastPageClicked()}
                class="${this.currentPage === this.totalPages ? 'active' : ''}">
                ${this.totalPages}
            </a>
        </li>
        ` : html``;
    }

    render() {
        return html`
        <ul class="vv-pagination">
            <li>
                <a aria-label="Previous" @click=${() => this._handlePreviousPageClicked()}
                    class="${this.currentPage === 1 ? 'disabled' : ''}">
                    <vv-icon-caret-right class="inline-block transform rotate-180" aria-hidden="true">
                    </vv-icon-caret-right>
                </a>
            </li>

            ${this._renderFirstPageLink()}     
            
            ${this.displayedPages.map((page) => this._renderDisplayedPage(page))}

            ${this._renderLastPageLink()}

            <li>
                <a aria-label="Next" @click=${() => this._handleNextPageClicked()}
                    class="${this.currentPage === this.totalPages ? 'disabled' : ''}">
                    <vv-icon-caret-right class="inline-block" aria-hidden="true"></vv-icon-caret-right>
                </a>
            </li>
        </ul>
        `
    }
}

class DisplayPage {
    constructor(value: number, display: string) {
        this.value = value;
        this.display = display;
    }

    value: number;
    display: string;
}
