import { html, customElement, property } from 'lit-element';
import numeral from 'numeral';

import { VvElement } from '../../elements/vv-element';

@customElement('vv-kpi-comparison')
export class VvKpiComparison extends VvElement {

    @property({ type : Number })
    value : number  = 0;

    @property({ type : Number })
    base : number = 0;

    @property()
    type : 'percent' | undefined = undefined;

    @property()
    label : string | undefined;

    @property()
    positiveColorClass = 'text-vv-green';

    @property()
    negativeColorClass = 'text-vv-red-dark';

    private _toPercent(value : number) {
        const percentage = value * 100;
        let fixed;

        if (percentage >= 10) {
            fixed = 0;
        } else if (percentage >= 1) {
            fixed = 1;
        } else {
            fixed = 2;
        }

        return parseFloat(percentage.toFixed(fixed));
    }

    private _toPercentString(value : number) {
        const percentage = value * 100;
        let format;

        if (percentage >= 10) {
            format = '0%';
        } else if (percentage >= 1) {
            format = '0.0%';
        } else {
            format = '0.00%';
        }

        return numeral(value).format(format);
    }

    private _getColorClass(value: number, base: number) {
        return value > base ? this.positiveColorClass: value < base ? this.negativeColorClass : '';
    }

    render() {
        if (this.base !== undefined) {
            const value = this.type === 'percent' ? this._toPercent(this.value) : this.value;
            const base = this.type === 'percent' ? this._toPercent(this.base) : this.base;
            const baseDisplay = this.type === 'percent' ? this._toPercentString(this.base) : numeral(this.base).format('0,0');
            const label = this.label !== undefined ? `${this.label} ` : '';
            const color = this._getColorClass(value, base);

            return html`<span class="${color}">${label}${baseDisplay}</span>`
        }

        return html`<span>-</span>`;
    }
}
