import { Context } from "../contracts";
import { MessageController } from "./MessageController";
import { ReactiveController, ReactiveControllerHost } from "./ReactiveController";

export const isDirty = () => {
  return _isDirty;
}
let _isDirty = false;

export class QueryController<T> implements ReactiveController {
  constructor(
    private host: ReactiveControllerHost,
    private messageController: MessageController,
    private context: () => Context,
    private query: () => Promise<T | undefined>,
  ) {
    host.addController(this);
    _isDirty = false;
  }

  private _serverHasNewData = false;
  get serverHasNewData() {
    return this._serverHasNewData;
  }
  set serverHasNewData(value) {
    this._serverHasNewData = value;
    this.host.requestUpdate();
  }

  private _isProcessing = false;
  get isProcessing() {
    return this._isProcessing;
  }
  set isProcessing(value) {
    this._isProcessing = value;
    this.host.requestUpdate();
  }

  private  _isDirty = false;
  get isDirty() {
    return this._isDirty;
  }
  set isDirty(value) {
    this._isDirty = value;
    _isDirty = value;
    this.host.requestUpdate();
  }

  private _data?: T = undefined;
  get data() {
    return this._data;
  }
  set data(value) {
    this._data = value;
    this.host.requestUpdate();
  }

  reload = async (isProcessing?: boolean) => {
    try {
      if (!this.data || isProcessing) {
        this.isProcessing = true;
      }
      const newData = await this.query();
      if (this.data && this.isDirty
        && (this.data as any)?.dataUpdated
        && (newData as any)?.dataUpdated
        && ((this.data as any)?.dateUpdated != (newData as any)?.dateUpdated)
      ) {
        this.serverHasNewData = true;
      } else if (!this.isDirty) {
        this.data = newData;
        this.serverHasNewData = false;
      } // else ignore the update since the user has pending changes.
    } finally {
      this.isProcessing = false;
    }
  };
  backgroundReload = async () => {
    if (!this.isProcessing) {
      await this.reload();
    }
  }

  async hostConnected() {
    reloadHandlers.set(this.query, this.backgroundReload);
    this.reload(); // load data for the first time.
  }

  hostDisconnected() {
    reloadHandlers.delete(this.query);
  }

  private readonly root = "/AdminApi/Twilio"; //"http://localhost:5000"; // "/api"
  
  getJson = async <T>(url: string) => {
    return await this.callApi<T>(url, undefined, 'get');
  }

  postJson = async <T>(url: string, body: any) => {
    this.isProcessing = true;
    return await this.callApi<T>(url, body, 'post');
  }

  deleteJson = async <T>(url: string, body: any) => {
    this.isProcessing = true;
    return await this.callApi<T>(url, body, 'delete');
  }

  private callApi = async <T>(url: string, body: any, method: 'get' | 'post' | 'delete') => {
    try {
      var r = await fetch(`${this.root}${url}`, {
        method: method, 
        body: JSON.stringify(body), 
        headers: new Headers({
          "Authorization": this.context().authorizationHeader ?? "",
          "content-type": "application/json",
        })
      });
      var j = await r.json();
      if (r.ok) {
        return j as T;
      } else {
        console.error(j);
        throw j.message || `${method} error with '${url}'`;
      }
    } catch (error) {
      this.messageController.clearAndSet(error as string);
      throw error;
    } finally { 
      this.isProcessing = false;
    }
  }
}

// Handle data reloading when the window is focused.
const reloadHandlers: Map<() => void, () => void> = new Map();
window.addEventListener('focus', async () => {
  reloadHandlers.forEach(reloadHandler => reloadHandler());
});
// Handle data reloading on a timer.
const timerLength = 60 * 1000; // Once per minute.
async function callReloadsOnTimer() {  
  reloadHandlers.forEach(reloadHandler => reloadHandler());
  setTimeout(callReloadsOnTimer, timerLength); // Check again 
}
setTimeout(callReloadsOnTimer, timerLength);
