import { html, customElement, property, state } from 'lit-element';
import dayjs from 'dayjs'

import { VvElement } from '../../elements/vv-element';
import { ifDefined } from '../../directives/if-defined';

const TIMEFRAMES = [
    { description: 'Last 7 Days', value: '-7 d' },
    { description: 'Last 30 Days', value: '-30 d' },
    { description: 'Last 90 Days', value: '-90 d' },
    { description: 'Last 6 Months', value: '-6 M' },
    { description: 'Last Year', value: '-1 y' },
    { description: 'Last 2 Years', value: '-2 y' },
    { description: 'Custom', value: 'custom' }
];

@customElement('vv-daterange-fancy')
export class VvDateRangeFancy extends VvElement {

    constructor() {
        super();

        this.addEventListener('start-changed', (e) => this._handleStartChanged(e));
        this.addEventListener('end-changed', (e) => this._handleEndChanged(e));
        this.addEventListener('modal-closed', () => this._handleCustomDateCanceled());
        this.addEventListener('modal-confirmed', () => this._handleCustomDateApplied());

        this._setDates(this.defaultValue);
    }

    @property()
    label: string = 'Select Date Range';

    @property()
    modalHeader: string = 'Custom Date Range';

    @property()
    modalConfirmText: string = 'Apply';

    @property()
    defaultValue: string = '-90 d';

    @property({attribute: false})
    startDate : Date | undefined;

    @property({attribute: false})
    endDate : Date | undefined;
    
    @property()
    options?: any;

    @state()
    _showDatePickerModal : boolean = false;

    @state()
    _customStartDate : Date | undefined;

    @state()
    _customEndDate : Date | undefined;

    @state()
    _startDateError : string | undefined;

    @state()
    _endDateError : string | undefined;

    _previousValue : string | undefined;
    _previousStartDate : Date | undefined;
    _previousEndDate : Date | undefined;
    
    _setDates(value: any) {
        // Store current values as previous values
        this._previousValue = value; 
        this._previousStartDate = this.startDate;
        this._previousEndDate = this.endDate;

        if (value !== 'custom' && value !== 'all') {
            const params = value.split(' ');
            const quantity = Number(params[0]);
            const unit = params[1];
    
            // 1 day is added as end date is now inclusive
            this.startDate = dayjs().startOf('day').add(quantity, unit).add(1, 'd').toDate();
            this.endDate = dayjs().endOf('day').toDate();
        }
        else if (value === 'all'){
            this.startDate = undefined;
            this.endDate = undefined;
        }

        this._onDateRangeChanged();
    }

    _handleSelectChanged($event : CustomEvent) {
        // @ts-ignore: value is custom set on vv-select
        const value = $event.target?.value;

        if (value === 'custom') {
            this._customStartDate = this.startDate;
            this._customEndDate = this.endDate;
            this._showDatePickerModal = true;
        } else {
            this._showDatePickerModal = false;
            this._setDates(value);
        }
    }

    _handleStartChanged(e : Event) {
        const $event = e as CustomEvent;
        const value = $event.detail.value;
        this._customStartDate = dayjs(value).isValid() ? dayjs(value).startOf('day').toDate() : undefined;
    }

    _handleEndChanged(e : Event) {
        const $event = e as CustomEvent;
        const value = $event.detail.value;
        this._customEndDate = dayjs(value).isValid() ? dayjs(value).endOf('day').toDate() : undefined;
    }

    _handleCustomDateCanceled() {
        const vvSelect = this.shadowRoot!.querySelector('vv-select')!;
        const select = vvSelect.shadowRoot!.querySelector('select')!;
        const previousOption = Array.from(select.options).find((option) => option.value === this._previousValue);

        this._customStartDate = this._previousStartDate;
        this._customEndDate = this._previousEndDate;
        this._resetDatePickerModal();
        select.selectedIndex = previousOption?.index || 0;
    }

    _handleCustomDateApplied() {
        if (this._customStartDate === undefined || this._customEndDate === undefined) {
            this._startDateError = this._customStartDate === undefined ? 'Required' : undefined;
            this._endDateError = this._customEndDate === undefined ? 'Required' : undefined;
        } else {
            this.startDate = this._customStartDate;
            this.endDate = this._customEndDate;

            this._resetDatePickerModal();
            this._setDates('custom');
        }
    }

    _resetDatePickerModal() {
        this._startDateError = undefined;
        this._endDateError = undefined;
        this._showDatePickerModal = false;
    }

    _onDateRangeChanged() {
        const event = new CustomEvent('daterange-changed', { detail: {
            startDate : this.startDate,
            endDate : this.endDate,
            selectedTimeframe : TIMEFRAMES.find((timeframe) => timeframe.value === this._previousValue)
        }});
        this.dispatchEvent(event);
    }

    _renderDateRangePicker() {
        const displayedStartDate = this._customStartDate ? dayjs(this._customStartDate).startOf('day').format('YYYY-MM-DD') : '';
        const displayedEndDate = this._customEndDate ? dayjs(this._customEndDate).endOf('day').format('YYYY-MM-DD') : '';

        return html`
            <vv-daterange-simple class="flex" startDate="${displayedStartDate}" endDate="${displayedEndDate}"
                startDateError="${ifDefined(this._startDateError)}" endDateError="${ifDefined(this._endDateError)}">
            </vv-daterange-simple>
        `;
    }

    _renderDateRangeButton() {
        const startDateFormat = dayjs(this.startDate).startOf('day').year() === dayjs(this.endDate).endOf('day').year() ? 'MMM D' : 'MMM D, YYYY';
        const displayedStartDate = this.startDate ? dayjs(this.startDate).startOf('day').format(startDateFormat) : '';
        const displayedEndDate = this.endDate ? dayjs(this.endDate).endOf('day').format('MMM D, YYYY') : '';

        return this._previousValue === 'custom' ? html`
            <vv-button type="tertiary" class="mt-2.5" @click=${() => this._showDatePickerModal = true}>
                ${displayedStartDate} - ${displayedEndDate}
            </vv-button>
        ` : html``;
    }

    render() {
        return html`
        <label>
            ${this.label}
            <vv-select
                class="w-full" 
                .options="${this.options || TIMEFRAMES}" 
                optionValueProperty="value"
                optionTextProperty="description"
                selectedValue=${this.defaultValue}
                @change=${this._handleSelectChanged}
            ></vv-select>
        </label>
        ${this._renderDateRangeButton()}
        <vv-modal class="w-1/3" modalBodyClass="overflow-visible" title=${this.modalHeader} closeOnConfirm="false" confirmText="${this.modalConfirmText}" ?visible=${this._showDatePickerModal}>
            <div>
                ${this._renderDateRangePicker()}
            </div>
        </vv-modal>
        `;
    }
}
