import { customElement, html, property } from "lit-element";
import { VvElement } from "../../elements/vv-element";

/** https://www.figma.com/file/lyszZNcNW7CtrR8QZE3SQk/VV-Reporting?node-id=1198%3A106 */
@customElement('vv-alert')
export class VvAlert extends VvElement {

  @property()
  type?: "danger" | "warning" | "info" = "danger";

  @property()
  closeText: string = '';

  @property({type: Boolean})
  showClose: boolean = true;

  _onClose() {
    const event = new CustomEvent('alert-closed', {bubbles: true, composed: true});
    this.dispatchEvent(event);
  }

  _renderIcon() {
    const t = this.type?.toLowerCase();
    const iconColor = t === "info" ? "text-vv-teal"
      : t === "warning" ? "text-vv-yellow-bright"
      : "text-vv-red-fire";

    return t === 'info' ? html`<vv-icon-info-circle class="flex-initial mr-5 ${iconColor}"></vv-icon-info-circle>` : html`<vv-icon-exclamation-circle class="flex-initial mr-5 ${iconColor}"></vv-icon-exclamation-circle>`;
  }

  _renderCloseText() {
    return this.closeText ? html`${this.closeText}` : html`<span class="sr-only">Close</span>`;
  }

  render() {
    const t = this.type?.toLowerCase();
    const backgroundColor = t === "info" ? "bg-white"
      : t === "warning" ? "bg-vv-yellow-light"
      : "bg-vv-red-light";
    return html`
      <div class="shadow-md rounded flex p-5 ${backgroundColor}" role="alert">
        ${this._renderIcon()}
        <div class="flex-auto">
          <slot></slot>
        </div>
	${this.showClose ? html`
        <div class="flex flex-col">
          <vv-button type="tertiary" @click=${this._onClose}>
            ${this._renderCloseText()}
            <vv-icon-close class="${this.closeText ? 'pl-1' : ''}" size="12"></vv-icon-close>
          </vv-button>
        </div>
	` : ''}
      </div>
    `;
  }  
}