import { css, html, customElement, property, state } from "lit-element";
import { BaseElement } from "./BaseElement";
import { isDirty } from "./Controllers/QueryController";

@customElement('twilio-trust-hub-admin')
export class TwilioTrustHubAdmin extends BaseElement {
  static styles = css`
    ${BaseElement.styles}
    /* :host {
    } */
  `;
  
  @property({type: Number})
  associationId = 0;
  @property()
  authorizationHeader = "";
  @property({type: Boolean})
  isSuperAdmin = false;
  @property()
  campaignInReviewMessage = "";

  @state()
  customerProfileSid: string = "";
  @state()
  trustProductSid: string = "";
  @state()
  brandRegistrationSid?: string = "";
  @state()
  campaignUseCaseSid: string = "";
  @state() 
  isStandardCustomerProfile: boolean = false;
  @state()
  isNonProfit: boolean = false;
  @state()
  isBrandRegistered: boolean = false;
  @state()
  private tab: "Customer Profiles" | "US A2P Brands" | "Campaign Use Cases" = "Customer Profiles";
  
  isTabCustomerProfiles = () => this.tab === "Customer Profiles";
  isTabUSA2PBrands = () => this.tab === "US A2P Brands";
  isTabCampaignUseCases = () => this.tab === "Campaign Use Cases";

  private _changeTabMsg = "Change tabs even though you have pending changes?";
  setTabBusinessProfile = () => { 
    if (!isDirty() || confirm(this._changeTabMsg)) {
      this.tab = "Customer Profiles";
    }
  }
  setTabUSA2PBrand = () => {
    if (!isDirty() || confirm(this._changeTabMsg)) {
      this.tab = "US A2P Brands";
    }
  }
  setTabCampaignUseCases = () => {
    if (!isDirty() || confirm(this._changeTabMsg)) {
      this.tab = "Campaign Use Cases";
    }
  }

  updated() {
    super.updated();
    // if (this.controller.usA2PBrands && (
    //   (!this.usA2PBrandSid && this.controller.usA2PBrands.length) // Pick the first Brand.
    //   || (this.usA2PBrandSid && !this.controller.usA2PBrands.some(x => x.sid === this.usA2PBrandSid))) // The Brand doesn't seem to exist anymore.
      
    // ) {
    //   this.usA2PBrandSid = this.controller.usA2PBrands?.[0]?.sid ?? "";
    //   this.campaignUseCaseSid = "";
    //   this.controller.clearAndReload();
    // }
    // // else if (!this.controller.campaignUseCase && this.controller.campaignUseCase) {
    //   //this.controller.clearAndReload();
    // // }
  }

  render() {
    return html`
  <div class="container mx-auto pb-8 twilio-trust-hub">
    <hr class="my-4" />
    <p>Use the form below to register your organization to send text messages through VoterVoice. The US carriers (AT&T, T-Mobile, Verizon, etc.) require that organizations sending text message broadcasts register through them.</p>
    <p>VoterVoice does not store any personal or business information you enter into this form. This gets sent directly to our provider, Twilio, who works with the carriers.</p>
    <div class="mt-4 flex flex-wrap gap-4">
      <button class="flex items-center ${this.isTabCustomerProfiles() ? 'btn-primary' : 'btn-default'}"
        @click=${this.setTabBusinessProfile}
        @setTabUSA2PBrand=${this.setTabUSA2PBrand}>
        Step 1: Business Profile
        <!-- ${this.incomplete()} -->
      </button>
      <button class="flex items-center ${this.isTabUSA2PBrands() ? 'btn-primary' : 'btn-default'}"
        ?disabled=${!this.customerProfileSid}
        @click=${this.setTabUSA2PBrand}
        @setTabBusinessProfile=${this.setTabBusinessProfile}
        @setTabCampaignUseCases=${this.setTabCampaignUseCases}>
        Step 2: Brand
        <!-- ${this.incomplete()} -->
      </button>
      <button class="flex items-center ${this.isTabCampaignUseCases() ? 'btn-primary' : 'btn-default'}"
        ?disabled=${!this.customerProfileSid || !this.trustProductSid}
        @click=${this.setTabCampaignUseCases}
        @setTabUSA2PBrand=${this.setTabUSA2PBrand}>
        Step 3: Campaign Use Cases
        <!-- ${this.incomplete()} -->
      </button>
    </div>
    ${this.isTabCustomerProfiles() ? html`
      <twilio-business-profiles .context=${this} 
        @setTabUSA2PBrand=${this.setTabUSA2PBrand}>
      </twilio-business-profiles>` : ''}
    ${this.isTabUSA2PBrands() ? html`
      <twilio-us-a2p-brands .context=${this} 
        @setTabBusinessProfile=${this.setTabBusinessProfile}
        @setTabCampaignUseCases=${this.setTabCampaignUseCases}>
      </twilio-us-a2p-brands>` : ''}
    ${this.isTabCampaignUseCases() ? html`
      <twilio-campaign-use-cases .context=${this}
        @setTabUSA2PBrand=${this.setTabUSA2PBrand}
        campaignInReviewMessage=${this.campaignInReviewMessage}>
      </twilio-campaign-use-cases>` : ''}
  </div>
    `;
  }

  incomplete() {
    return html`
    <vv-tooltip class="ml-2 inline-flex">
      <vv-icon-info-circle class="inline-flex"></vv-icon-info-circle>
      <span slot="tooltiptext">This step is incomplete.</span>
    </vv-tooltip>
    `;
  } 
}